import React, { useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import IconButton from "@material-ui/core/IconButton";
import Snackbar from "components/Snackbar/Snackbar.js";
// Icons
import DeleteIcon from "@material-ui/icons/Delete";
import styles from "assets/jss/material-dashboard-react/components/tableStyle.js";

const useStyles = makeStyles(styles);

export default function CustomTableEdit(props) {
  const { tableHead, tableData, tableHeaderColor, maj, setMaj, setCommandes } =
    props;

  const classes = useStyles();
  const [tc, setTC] = useState(false);
  const [message, setMessage] = useState("");
  const [color, setColor] = useState("primary");

  const handleSnackbar = (cl, ms) => {
    setColor(cl);
    setMessage(ms);
    setTC(true);
    setTimeout(function () {
      setTC(false);
      setColor("");
      setMessage("");
    }, 4000);
  };

  const onDelete = (id) => {
    let temp = tableData;
    console.log(
      id,
      temp.find((e) => e.id === id)
    );
    // temp.splice(id, 1);
    // for (let i = 0; i < temp.length; i++) {
    //   if (i >= id) {
    //     temp[i].id = temp[i].id - 1;
    //   }
    // }
    temp = temp.filter(function (obj) {
      return obj.id !== id;
    });
    setCommandes(temp);
    setMaj(~maj);
  };

  return (
    <div>
      <Snackbar
        place="tc"
        color={color}
        message={message}
        open={tc}
        closeNotification={() => setTC(false)}
        close
      />
      <Paper className={classes.root} style={{ marginTop: "0px" }}>
        <Table
          stickyHeader
          className={classes.table}
          aria-label="caption table"
        >
          <TableHead className={classes[tableHeaderColor + "TableHeader"]}>
            <TableRow
              className={classes.tableHeadRow}
              style={{ height: "40px" }}
            >
              <TableCell></TableCell>
              {tableHead.map((prop) => {
                return prop === "id" ? null : (
                  <TableCell
                    className={classes.tableCell + " " + classes.tableHeadCell}
                    key={prop.id}
                  >
                    {prop}
                  </TableCell>
                );
              })}
            </TableRow>
          </TableHead>
          <TableBody>
            {tableData !== undefined
              ? tableData.map((row, key) => (
                  <TableRow className={classes.tableRow} key={key * 100}>
                    <TableCell className={classes.selectTableCell}>
                      <IconButton
                        style={{ padding: "5px" }}
                        aria-label="delete"
                        onClick={() => onDelete(row.id)}
                      >
                        <DeleteIcon />
                      </IconButton>
                    </TableCell>
                    {tableHead.map((item, key) => {
                      return item === "id" ? null : (
                        <TableCell
                          style={{
                            fontSize: "0.85rem",
                            fontWeight: "300",
                            padding: "5px",
                          }}
                          key={key}
                        >
                          {row[item]}
                        </TableCell>
                      );
                    })}
                  </TableRow>
                ))
              : null}
          </TableBody>
        </Table>
      </Paper>
    </div>
  );
}
