import { useState, useEffect } from "react";
import CardMat from "components/Card/Card.js";
import Snackbar from "components/Snackbar/Snackbar.js";
import { authFetch } from "auth";
import AddInventaireDialog from "../components/Dialog/AddInventaireDialog";
import AddEntreAlimentDialog from "../components/Dialog/AddEntreAlimentDialog";
import {
  CardHeader,
  Grid,
  FormControl,
  Select,
  MenuItem,
  makeStyles,
  TextField,
  FormHelperText,
} from "@material-ui/core";
import CustomTableEdit from "components/TableStock";
import CardBody from "components/Card/CardBody.js";
import { styled } from "@material-ui/styles";
import VoirVentes from "components/Dialog/VoirVentes";

const styles = (theme) => ({
  select: {
    color: "white",
    "&:before": {
      borderColor: "white",
      color: "white",
    },
    "&:after": {
      borderColor: "white",
    },
  },
  multilineColor: {
    color: "white",
  },
  input: {
    root: {
      "& .MuiInputBase-root.Mui-disabled": {
        color: "rgba(0, 0, 0,0.0)",
      },
      "& .MuiFormLabel-root.Mui-disabled": {
        color: "rgba(0, 0, 0,0.0)",
      },
    },
  },
  icon: {
    fill: "white",
  },
});

const MyTextField = styled(TextField)({
  "& label.Mui-focused": {
    color: "white",
  },
  "& .MuiFormLabel-root": {
    color: "white",
  },
  "& .MuiInput-underline:after": {
    borderBottomColor: "white",
  },
  "& .MuiInput-underline:before": {
    borderBottomColor: "white",
  },
  "& .MuiOutlinedInput-root": {
    "& fieldset": {
      borderColor: "red",
    },
    "&:hover fieldset": {
      borderColor: "yellow",
    },
    "&.Mui-focused fieldset": {
      borderColor: "green",
    },
  },
});

const useStyles = makeStyles(styles);

export default function Stocks() {
  const [stocks, setStocks] = useState([]);
  const [alimentList, setAlimentList] = useState([]);
  const [filtre, setFiltre] = useState(2);
  const [dateDebut, setDateDebut] = useState("10/12/2021");
  const [tableHeaders] = useState([
    "Fecha",
    "Alimentacion",
    "Compras",
    "Ventas",
    "Diff de stock",
    "Stock",
    "Comentario",
  ]);
  const [maj, setMaj] = useState(true);

  const [tc, setTC] = useState(false);
  const [message, setMessage] = useState("");
  const [color, setColor] = useState("primary");

  const classes = useStyles();

  let dateOneYear = new Date();
  dateOneYear.setMonth(dateOneYear.getMonth() - 12);

  useEffect(() => {
    console.log(alimentList);
  }, [alimentList]);

  useEffect(() => {
    if (dateDebut.length === 10) {
      const date =
        dateDebut.substring(6, 10) +
        "-" +
        dateDebut.substring(3, 5) +
        "-" +
        dateDebut.substring(0, 2);
      const url = "/api/stocks/" + filtre + "/" + date;
      authFetch(url)
        .then((response) => {
          if (response.ok) {
            return response.json();
          } else {
            throw new Error(response.statusText);
          }
        })
        .then((response) => {
          setStocks(response.stocks);
          setAlimentList(response.alimentList);
        })
        .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
    }
  }, [maj, filtre, dateDebut]);

  const handleSnackbar = (cl, ms) => {
    setColor(cl);
    setMessage(ms);
    setTC(true);
    setTimeout(function () {
      setTC(false);
      setColor("");
      setMessage("");
    }, 4000);
  };

  return (
    <Grid container spacing={1} justify="center" alignItems="center">
      <Snackbar
        place="tc"
        color={color}
        message={message}
        open={tc}
        closeNotification={() => setTC(false)}
        close
      />
      {alimentList.length !== 0 ? (
        <Grid container item xs={12} justify="center" alignItems="center">
          <Grid item style={{ margin: "5px" }}>
            <AddInventaireDialog
              alimentsList={alimentList}
              maj={maj}
              setMaj={setMaj}
              handleSnackbar={handleSnackbar}
            />
          </Grid>
          <Grid item>
            <AddEntreAlimentDialog
              alimentsList={alimentList}
              maj={maj}
              setMaj={setMaj}
              handleSnackbar={handleSnackbar}
            />
          </Grid>
        </Grid>
      ) : null}
      <Grid item xs={12}>
        {tableHeaders === "" || stocks.length === 0 ? null : (
          <CardMat style={{ marginTop: "0px" }}>
            <CardHeader
              style={{
                color: "white",
                backgroundColor: "#3f51b5",
                fontFamily: '"Roboto", "Helvetica", "Arial", sans-serif',
                fontSize: "34px",
                fontWeight: 400,
                lineHeight: 1.235,
              }}
              color="info"
              title="Almacen"
              action={
                <Grid
                  container
                  spacing={1}
                  justify="center"
                  alignItems="center"
                >
                  <Grid item xs={4}>
                    <FormControl
                      style={{
                        backgroundColor: "#f50057",
                        color: "fff",
                        boxShadow:
                          "0px 3px 1px -2px rgb(0 0 0 / 20%), 0px 2px 2px 0px rgb(0 0 0 / 14%), 0px 1px 5px 0px rgb(0 0 0 / 12%)",
                        borderRadius: "4px",
                        marginRight: "8px",
                        padding: 8,
                      }}
                    >
                      <MyTextField
                        InputProps={{
                          className: classes.multilineColor,
                        }}
                        required
                        color="warning"
                        label="Date dd/MM/yyyy"
                        type="text"
                        value={dateDebut}
                        onChange={(e) => setDateDebut(e.target.value)}
                      />
                    </FormControl>
                  </Grid>
                  <Grid item xs={4}>
                    <FormControl
                      style={{
                        backgroundColor: "#f50057",
                        color: "fff",
                        boxShadow:
                          "0px 3px 1px -2px rgb(0 0 0 / 20%), 0px 2px 2px 0px rgb(0 0 0 / 14%), 0px 1px 5px 0px rgb(0 0 0 / 12%)",
                        borderRadius: "4px",
                        marginRight: "8px",
                        padding: 8,
                      }}
                    >
                      <Select
                        className={classes.select}
                        inputProps={{
                          classes: {
                            icon: classes.icon,
                          },
                        }}
                        labelId="aliment-label"
                        id="aliment-select"
                        value={filtre}
                        onChange={(e) => setFiltre(e.target.value)}
                      >
                        {alimentList.map((aliment, key) => (
                          <MenuItem key={key} value={aliment[0]}>
                            {aliment[1]}
                          </MenuItem>
                        ))}
                      </Select>
                      <FormHelperText style={{ color: "white" }}>
                        Filtrar por alimento
                      </FormHelperText>
                    </FormControl>
                  </Grid>
                  <Grid item xs={4}>
                    <VoirVentes
                      maj={maj}
                      setMaj={setMaj}
                      filtre={filtre}
                      nomAliment={
                        alimentList.length !== 0
                          ? alimentList.find((a) => a[0] === filtre)[1]
                          : ""
                      }
                      date={dateDebut}
                    ></VoirVentes>
                  </Grid>
                </Grid>
              }
            ></CardHeader>
            <CardBody>
              <CustomTableEdit
                tableHeaderColor="info"
                tableHead={tableHeaders}
                tableData={stocks}
                postUrl={"/api/stocks"}
                setMaj={setMaj}
                maj={maj}
              />
            </CardBody>
          </CardMat>
        )}
      </Grid>
    </Grid>
  );
}
