import React, { useState } from "react";
import { authFetch } from "auth";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import { FormControlLabel, Grid, Checkbox } from "@material-ui/core";

export default function FormDialog({
  cycle,
  handleSnackbar,
  cyclesList,
  setCycle,
  setCyclesList,
  poidsAM,
  poidsPM,
  setMaj,
  date,
  maj,
}) {
  const [open, setOpen] = useState(false);
  const [change, setChange] = useState(false);

  const handleConfirm = () => {
    let temp = cyclesList;
    temp[cycle.id] = {
      ...temp[cycle.id],
      ali: true,
      Poids: poidsAM + poidsPM,
      Poids_pm: poidsPM,
      maj: true,
      Type_Aliment: cycle.Type_Aliment,
    };
    setCyclesList(temp);
    var url = "/api/alimentation";
    const requestOptions = {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        bassin: cycle.Bassin,
        poids: poidsAM + poidsPM,
        poids_pm: poidsPM,
        date: date,
        aliment: cycle.Type_Aliment,
        change: change,
      }),
    };
    authFetch(url, requestOptions)
      .then((response) => {
        if (response.status === 401) {
          return null;
        } else if (response.status === 500) {
          return null;
        }
        return response.json();
      })
      .then((response) => {
        response !== null
          ? handleSnackbar("success", response.message)
          : handleSnackbar("warning", "Bad request");
      });
    setMaj(~maj);
    setOpen(false);
    setCycle(undefined);
  };

  const handleOpen = () => {
    if (cyclesList[cycle.id].Type_Aliment !== cycle.Type_Aliment) {
      setOpen(true);
    } else {
      handleConfirm();
    }
  };

  return (
    <div>
      <Button
        id="ButtonDialog"
        variant="contained"
        color="primary"
        onClick={() => handleOpen()}
      >
        Confirmar
      </Button>
      <Dialog
        open={open}
        onClose={() => {
          setOpen(false);
        }}
        aria-labelledby="form-dialog-title"
      >
        <DialogTitle id="form-dialog-title">Cambio de alimento</DialogTitle>
        <DialogContent>
          <Grid container spacing={1} justify="center" alignItems="center">
            <Grid item xs={12}>
              <FormControlLabel
                control={
                  <Checkbox
                    checked={change}
                    onChange={(e) => setChange(e.target.checked)}
                  />
                }
                label="Modificar el alimento para los dias siguientes"
              />
            </Grid>
          </Grid>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleConfirm} color="primary">
            Confirmar
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
