import { useState, useEffect } from "react";
import {
  CardHeader,
  Card,
  CardContent,
  Grid,
  Typography,
  TextField,
} from "@material-ui/core";
import Table from "components/Table";
import CardMat from "components/Card/Card.js";
import CardHeaderMat from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import Snackbar from "components/Snackbar/Snackbar.js";
import { authFetch } from "auth";

export default function Clients({
  maj,
  setMaj,
  filtre,
  defaultDate,
  nomAliment,
}) {
  const [tableHeaders] = useState(["Client", "Quantite", "Type aliments"]);
  const [tableData, setTableData] = useState("");
  const [date, setDate] = useState(
    defaultDate === undefined ? "" : defaultDate
  );
  //
  const [tc, setTC] = useState(false);
  const [message, setMessage] = useState("");
  const [color, setColor] = useState("primary");

  useEffect(() => {
    if (date.length === 10) {
      let dateF =
        date.substring(6, 10) +
        "-" +
        date.substring(3, 5) +
        "-" +
        date.substring(0, 2);
      const url = "/api/ventesParJour/" + dateF + "/" + filtre;
      authFetch(url)
        .then((response) => {
          if (response.ok) {
            return response.json();
          } else {
            throw new Error(response.statusText);
          }
        })
        .then((response) => {
          console.log(response.ventes);
          setTableData(response.ventes);
        })
        .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
    }
  }, [date, filtre, maj]);

  const handleSnackbar = (cl, ms) => {
    setColor(cl);
    setMessage(ms);
    setTC(true);
    setTimeout(function () {
      setTC(false);
      setMessage("");
    }, 4000);
  };

  return (
    <Grid container spacing={1} justify="center" alignItems="center">
      <Snackbar
        place="tc"
        color={color}
        message={message}
        open={tc}
        closeNotification={() => setTC(false)}
        close
      />
      <Grid item xs={12}>
        <Card>
          <CardHeader
            title={
              filtre !== undefined
                ? "Venta por dia de " + nomAliment
                : "Venta por dia"
            }
            action={
              <Grid item>
                <TextField
                  required
                  label="Fecha dd/MM/yyyy"
                  type="text"
                  value={date}
                  onChange={(e) => setDate(e.target.value)}
                />
              </Grid>
            }
          />
          <CardContent>
            <Grid container spacing={1} justify="center" alignItems="center">
              <Grid item xs={12}>
                {tableHeaders === "" || tableData === "" ? null : (
                  <CardMat style={{ marginTop: "50px" }}>
                    <CardHeaderMat color="info">
                      <Typography variant="h4">Compra de {date}</Typography>
                    </CardHeaderMat>
                    <CardBody>
                      <Table
                        tableHeaderColor="info"
                        tableHead={tableHeaders}
                        tableData={tableData}
                        maj={maj}
                        setMaj={setMaj}
                      />
                    </CardBody>
                  </CardMat>
                )}
              </Grid>
            </Grid>
          </CardContent>
        </Card>
      </Grid>
    </Grid>
  );
}
