import React, { useState, useEffect } from "react";
import Button from "@material-ui/core/Button";
import { authFetch } from "auth";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import { makeStyles } from "@material-ui/core/styles";
import "assets/css/App.css";
import {
  FormControl,
  FormHelperText,
  InputLabel,
  Select,
  MenuItem,
  Grid,
  TextField,
} from "@material-ui/core";

const styles = (theme) => ({
  select: {
    color: "white",
    "&:before": {
      borderColor: "white",
      color: "white",
    },
    "&:after": {
      borderColor: "white",
    },
  },
  icon: {
    fill: "white",
  },
});

const useStyles = makeStyles(styles);

export default function FormDialog({
  handleSnackbar,
  setEspece,
  espece,
  maj,
  setMaj,
}) {
  const [bassinsList, setBassinsList] = useState([""]);
  const [especesList, setEspecesList] = useState([""]);
  const [especeAdd, setEspeceAdd] = useState("");
  const [open, setOpen] = useState(false);
  const [open2, setOpen2] = useState(false);
  const [bassin, setBassin] = useState("");
  const [bassin2, setBassin2] = useState("tous");
  const [date, setDate] = useState("");
  const [dateDebut, setDateDebut] = useState("");
  const [dateFin, setDateFin] = useState("");

  const classes = useStyles();

  useEffect(() => {
    var url = "/api/especes";
    authFetch(url)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        setBassinsList(response.bassins);
        setEspecesList(response.especes);
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
  }, [handleSnackbar]);

  const handleAddClose = () => {
    if (date.length !== 10) {
      handleSnackbar("danger", "Formato de fecha incorrecto");
    } else {
      setOpen(false);
      var url = "/api/cycles";
      const requestOptions = {
        method: "PUT",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          Bassin: bassin,
          espece: especeAdd,
          Date:
            date.substring(6, 10) +
            "-" +
            date.substring(3, 5) +
            "-" +
            date.substring(0, 2),
        }),
      };
      authFetch(url, requestOptions)
        .then((response) => {
          if (response.status === 401) {
            return null;
          } else if (response.status === 500) {
            return null;
          }
          return response.json();
        })
        .then((response) => {
          response !== null
            ? handleSnackbar("success", response.message)
            : handleSnackbar("warning", "Bad request");
          setMaj(~maj);
        });
    }
  };

  return (
    <div>
      <FormControl
        style={{
          backgroundColor: "#f50057",
          color: "fff",
          boxShadow:
            "0px 3px 1px -2px rgb(0 0 0 / 20%), 0px 2px 2px 0px rgb(0 0 0 / 14%), 0px 1px 5px 0px rgb(0 0 0 / 12%)",
          borderRadius: "4px",
          marginRight: "8px",
          padding: 8,
        }}
      >
        <Select
          className={classes.select}
          inputProps={{
            classes: {
              icon: classes.icon,
            },
          }}
          labelId="espece-label"
          id="espece-select"
          value={espece}
          onChange={(e) => setEspece(e.target.value)}
        >
          <MenuItem value="Tout">Todo</MenuItem>
          <MenuItem value="tilapia">Tilapia</MenuItem>
          <MenuItem value="crevette">Camarones</MenuItem>
          <MenuItem value="carpe">Carpa</MenuItem>
          <MenuItem value="colossoma">Colossoma</MenuItem>
        </Select>
        <FormHelperText style={{ color: "white" }}>
          Filtrar por especies
        </FormHelperText>
      </FormControl>
      <Button
        id="ButtonDialog"
        variant="contained"
        color="secondary"
        onClick={() => setOpen2(true)}
        style={{ marginRight: "0.5em" }}
      >
        Ver ciclos anteriores
      </Button>
      <Button
        id="ButtonDialog"
        variant="contained"
        color="secondary"
        onClick={() => setOpen(true)}
      >
        Añadir un ciclo
      </Button>
      <Dialog
        open={open}
        onClose={() => {
          setOpen(false);
          setBassin("");
          setDate("");
        }}
        aria-labelledby="form-dialog-title"
      >
        <DialogTitle id="form-dialog-title"> Añadir un ciclo</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Seleccione un estanque y la fecha de llenado.{" "}
          </DialogContentText>
          <Grid container spacing={1}>
            <Grid item>
              <FormControl style={{ minWidth: "150px" }}>
                <InputLabel id="bassin-label">Estanque</InputLabel>
                <Select
                  labelId="bassin-label"
                  id="bassin-select"
                  value={bassin}
                  onChange={(e) => setBassin(e.target.value)}
                >
                  {bassinsList.map((Bassin, key) => (
                    <MenuItem key={key} value={Bassin[0]}>
                      {Bassin[1]}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>
            <Grid item>
              <FormControl style={{ minWidth: "150px" }}>
                <InputLabel id="espece-label">Espece</InputLabel>
                <Select
                  labelId="espece-label"
                  id="espece-select"
                  value={especeAdd}
                  onChange={(e) => setEspeceAdd(e.target.value)}
                >
                  {especesList.map((espece, key) => (
                    <MenuItem key={key} value={espece[0]}>
                      {espece[1]}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>
            <Grid item>
              <TextField
                required
                label="Date dd/MM/yyyy"
                type="text"
                value={date}
                onChange={(e) => setDate(e.target.value)}
              />
            </Grid>
          </Grid>
        </DialogContent>
        <DialogActions>
          <Button
            onClick={() => {
              setOpen(false);
              setBassin("");
              setDate("");
            }}
            color="primary"
          >
            Cancelar
          </Button>
          <Button onClick={handleAddClose} color="primary">
            Añadir
          </Button>
        </DialogActions>
      </Dialog>
      <Dialog
        open={open2}
        onClose={() => {
          setOpen2(false);
        }}
        aria-labelledby="form-dialog-title"
      >
        <DialogTitle id="form-dialog-title"> Ver ciclos anteriores</DialogTitle>
        <DialogContent>
          <Grid container spacing={1}>
            <Grid item>
              <FormControl style={{ minWidth: "150px" }}>
                <InputLabel id="bassin-label">Estanque</InputLabel>
                <Select
                  labelId="bassin-label"
                  id="bassin-select"
                  value={bassin2}
                  onChange={(e) => setBassin2(e.target.value)}
                >
                  <MenuItem value={"tous"}>Tous</MenuItem>
                  {bassinsList.map((Bassin, key) => (
                    <MenuItem key={key} value={Bassin[0]}>
                      {Bassin[1]}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>
            <Grid item xs={4}>
              <TextField
                required
                label="Date début"
                type="text"
                value={dateDebut}
                onChange={(e) => setDateDebut(e.target.value)}
              />
            </Grid>
            <Grid item xs={4}>
              <TextField
                required
                label="Date fin"
                type="text"
                value={dateFin}
                onChange={(e) => setDateFin(e.target.value)}
              />
            </Grid>
          </Grid>
        </DialogContent>
        <DialogActions>
          <Button
            onClick={() => {
              setOpen2(false);
            }}
            color="primary"
          >
            Cancelar
          </Button>
          <Button
            href={
              "/copeyito/ancienCycle/" +
              bassin2 +
              "/" +
              dateDebut.substring(6, 10) +
              "-" +
              dateDebut.substring(3, 5) +
              "-" +
              dateDebut.substring(0, 2) +
              "/" +
              dateFin.substring(6, 10) +
              "-" +
              dateFin.substring(3, 5) +
              "-" +
              dateFin.substring(0, 2)
            }
            color="primary"
          >
            Voir
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
