import React, { useState } from "react";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import { authFetch } from "auth";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import {
  FormControl,
  Grid,
  InputLabel,
  MenuItem,
  Select,
} from "@material-ui/core";

export default function FormDialog({
  lot,
  handleSnackbar,
  setMaj,
  maj,
  espece,
}) {
  const [open, setOpen] = useState(false);
  const [peches, setPeches] = useState({
    date: "",
    poids: "",
    quantitelb: "",
    quantite: "",
    commentaire: "",
    destination: "",
  });

  const handleConfirm = () => {
    if (peches.date.length !== 10) {
      handleSnackbar("danger", "Formato de fecha incorrecto");
    } else {
      var url = "/api/peches";
      const requestOptions = {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          ...peches,
          date:
            peches.date.substring(6, 10) +
            "-" +
            peches.date.substring(3, 5) +
            "-" +
            peches.date.substring(0, 2),
          poids: parseFloat(peches.poids),
          quantite:
            espece.substring(0, 9) === "Juvéniles" ||
            espece.substring(0, 10) === "Postlarves"
              ? parseFloat(peches.quantitelb) * parseFloat(peches.poids)
              : parseFloat(peches.quantite),
          lot: lot,
        }),
      };
      authFetch(url, requestOptions)
        .then((response) => {
          if (response.ok) {
            return response.json();
          } else {
            throw new Error(response.statusText);
          }
        })
        .then((response) => {
          handleSnackbar("success", response.message);
          setMaj(maj + 1);
        })
        .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
      setOpen(false);
      setPeches({
        date: "",
        poids: "",
        quantitelb: "",
        commentaire: "",
        destination: "",
      });
    }
  };

  return (
    <div>
      <Button
        id="ButtonDialog"
        variant="contained"
        color="secondary"
        onClick={() => setOpen(true)}
      >
        Añadir una pesca
      </Button>
      <Dialog
        open={open}
        onClose={() => {
          setOpen(false);
        }}
        aria-labelledby="form-dialog-title"
      >
        <DialogTitle id="form-dialog-title">Añadir una pesca</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Elija la fecha de siembra, el peso total y la cantitad por libras.
          </DialogContentText>
          <Grid container spacing={1} justify="center" alignItems="center">
            <Grid item xs={12}>
              <TextField
                required
                label="Date dd/MM/yyyy"
                type="text"
                value={peches.date}
                onChange={(e) => setPeches({ ...peches, date: e.target.value })}
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                required
                label="Peso total"
                type="text"
                value={peches.poids}
                onChange={(e) =>
                  setPeches({
                    ...peches,
                    poids: e.target.value,
                  })
                }
              />
            </Grid>
            <Grid item xs={12}>
              {espece &&
              (espece.substring(0, 9) === "Juvéniles" ||
                espece.substring(0, 10) === "Postlarves") ? (
                <TextField
                  required
                  label={"Cantitad por libras"}
                  type="text"
                  value={peches.quantitelb}
                  onChange={(e) =>
                    setPeches({
                      ...peches,
                      quantitelb: e.target.value,
                    })
                  }
                />
              ) : (
                <TextField
                  required
                  label={"Cantitad total"}
                  type="text"
                  value={peches.quantite}
                  onChange={(e) =>
                    setPeches({
                      ...peches,
                      quantite: e.target.value,
                    })
                  }
                />
              )}
            </Grid>
            <Grid item xs={12}>
              <TextField
                label="Comentario"
                type="text"
                value={peches.commentaire}
                onChange={(e) =>
                  setPeches({ ...peches, commentaire: e.target.value })
                }
              />
            </Grid>
            <Grid item xs={12}>
              <FormControl style={{ minWidth: "200px" }} required>
                <InputLabel id="Destination-label">Destino</InputLabel>
                <Select
                  labelId="Destination-label"
                  id="Destination-select"
                  value={peches.destination}
                  onChange={(e) =>
                    setPeches({ ...peches, destination: e.target.value })
                  }
                >
                  <MenuItem value={"V"}>Venta</MenuItem>
                  <MenuItem value={"S"}>Siembra</MenuItem>
                  <MenuItem value={"A"}>Otro</MenuItem>
                </Select>
              </FormControl>
            </Grid>
          </Grid>
        </DialogContent>
        <DialogActions>
          <Button
            onClick={() => {
              setOpen(false);
            }}
            color="primary"
          >
            Annuler
          </Button>
          <Button onClick={handleConfirm} color="primary">
            Ajouter
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
