import React, { useState, useEffect } from "react";
import "assets/css/App.css";
import { authFetch } from "auth";
import CustomTableEdit from "components/CyclesTable";
import Card from "components/Card/Card.js";
import CardBody from "components/Card/CardBody.js";
import { Grid, CardHeader } from "@material-ui/core";
import FormDialog from "components/Dialog/AddCycleDialog";
import Snackbar from "components/Snackbar/Snackbar.js";

export default function Cycles() {
  const [tableHeaders, setTableHeaders] = useState("");
  const [tableData, setTableData] = useState("");
  const [tc, setTC] = useState(false);
  const [message, setMessage] = useState("");
  const [color, setColor] = useState("primary");
  const [espece, setEspece] = useState("Tout");
  const [maj, setMaj] = useState(true);

  useEffect(() => {
    var url = "/api/cycles/" + espece;
    authFetch(url)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        setTableHeaders([
          "Estanque",
          "Fecha lleno",
          "Fecha ultima pesca",
          "# meses",
          "Alimentacion",
          "Alimentacion téo",
          "Superficie",
          "Especie",
          "Peso sembrado",
          "Peso pescado",
          "Peso actual",
          "Mortalidad",
          "Rendimiento",
          "Alimentacion total",
          "Indice de conversion",
        ]);
        setTableData(response.cycles);
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
  }, [espece, tc, maj]);

  const handleSnackbar = (cl, ms) => {
    setColor(cl);
    setMessage(ms);
    setTC(true);
    setTimeout(function () {
      setTC(false);
      setColor("");
      setMessage("");
    }, 4000);
  };

  return (
    <div id="#copeyito">
      <Grid container spacing={1}>
        <Snackbar
          place="tc"
          color={color}
          message={message}
          open={tc}
          closeNotification={() => setTC(false)}
          close
        />
        <Grid item xs={12} align="center">
          {tableHeaders === "" || tableData === "" ? null : (
            <Card style={{ marginBottom: "0px" }}>
              <CardHeader
                id="Header"
                color="primary"
                title="Ciclos"
                action={
                  <FormDialog
                    handleSnackbar={(cl, ms) => handleSnackbar(cl, ms)}
                    setEspece={setEspece}
                    espece={espece}
                    maj={maj}
                    setMaj={setMaj}
                  />
                }
              />
              <CardBody style={{ padding: "3px" }}>
                <CustomTableEdit
                  tableHeaderColor="primary"
                  tableHead={tableHeaders}
                  setMessage
                  tableData={tableData.sort((a, b) =>
                    a.Estanque > b.Estanque ? 1 : -1
                  )}
                  postUrl="/api/cycles"
                  newLine={false}
                  handleSnackbar={handleSnackbar}
                  maj={maj}
                  setMaj={setMaj}
                />
                <br />
              </CardBody>
            </Card>
          )}
        </Grid>
      </Grid>
    </div>
  );
}
