import React, { useState, useEffect } from "react";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import { authFetch } from "auth";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import {
  FormControl,
  Grid,
  InputLabel,
  MenuItem,
  Select,
} from "@material-ui/core";

export default function FormDialog({ type, handleSnackbar, setMaj, maj }) {
  const [open, setOpen] = useState(false);
  const [especeList, setEspeceList] = useState([]);
  const [newData, setNewData] = useState({});

  useEffect(() => {
    var url = "/api/especes";
    authFetch(url)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        setEspeceList(response.especes);
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
  }, [handleSnackbar]);

  const handleConfirm = () => {
    var url = "/api/addInTable/" + type;
    const requestOptions = {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ ...newData }),
    };
    authFetch(url, requestOptions)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        handleSnackbar("success", response.message);
        setMaj(~maj);
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
    setOpen(false);
    setNewData({});
  };

  return (
    <div>
      <Button
        id="ButtonDialog"
        variant="contained"
        color="primary"
        onClick={() => setOpen(true)}
      >
        Añadir
      </Button>
      <Dialog
        open={open}
        onClose={() => {
          setOpen(false);
        }}
        aria-labelledby="form-dialog-title"
      >
        <DialogTitle id="form-dialog-title">Añadir</DialogTitle>
        <DialogContent>
          <DialogContentText>Oui</DialogContentText>
          <Grid container spacing={1} justify="center" alignItems="center">
            {/* Nouveau bassin, Especes, Pompes, TypeAliment  */}
            {["Bassins", "Especes", "Pompes", "TypeAliment"].includes(type) ? (
              <Grid item xs={12}>
                <TextField
                  label="Libelle"
                  type="text"
                  value={newData.libelle}
                  onChange={(e) =>
                    setNewData({ ...newData, libelle: e.target.value })
                  }
                />
              </Grid>
            ) : null}
            {/* Nouveau bassin  */}
            {["Bassins"].includes(type) ? (
              <Grid item xs={12}>
                <TextField
                  label="surface"
                  type="number"
                  value={newData.surface}
                  onChange={(e) =>
                    setNewData({ ...newData, surface: e.target.value })
                  }
                />
              </Grid>
            ) : null}
            {["Bassins"].includes(type) ? (
              <Grid item xs={12}>
                <TextField
                  label="profondeur"
                  type="number"
                  value={newData.profondeur}
                  onChange={(e) =>
                    setNewData({ ...newData, profondeur: e.target.value })
                  }
                />
              </Grid>
            ) : null}
            {/* Nouvelle espèce */}
            {["Especes"].includes(type) ? (
              <Grid item xs={12}>
                <TextField
                  label="espece"
                  type="text"
                  value={newData.espece}
                  onChange={(e) =>
                    setNewData({ ...newData, espece: e.target.value })
                  }
                />
              </Grid>
            ) : null}
            {["Especes"].includes(type) ? (
              <Grid item xs={12}>
                <FormControl style={{ minWidth: "200px" }} required>
                  <InputLabel id="Especie-label">Type</InputLabel>
                  <Select
                    labelId="Especie-label"
                    id="Especie-select"
                    value={newData.espece_id}
                    onChange={(e) =>
                      setNewData({ ...newData, type: e.target.value })
                    }
                  >
                    <MenuItem value={"carpe"}>Carpe</MenuItem>
                    <MenuItem value={"colossoma"}>Colossoma</MenuItem>
                    <MenuItem value={"tilapia"}>Tilapia</MenuItem>
                    <MenuItem value={"crevette"}>Crevettte</MenuItem>
                  </Select>
                </FormControl>
              </Grid>
            ) : null}
            {/* Nouvelle pompe */}
            {["Pompes"].includes(type) ? (
              <Grid item xs={12}>
                <TextField
                  label="Débit"
                  type="number"
                  value={newData.debit}
                  onChange={(e) =>
                    setNewData({ ...newData, debit: e.target.value })
                  }
                />
              </Grid>
            ) : null}
            {/* Nouvelle mortalite */}
            {["Mortalite"].includes(type) ? (
              <Grid item xs={12}>
                <FormControl style={{ minWidth: "200px" }} required>
                  <InputLabel id="Especie-label">Especie</InputLabel>
                  <Select
                    labelId="Especie-label"
                    id="Especie-select"
                    value={newData.espece_id}
                    onChange={(e) =>
                      setNewData({ ...newData, espece_id: e.target.value })
                    }
                  >
                    {especeList.map((e) => (
                      <MenuItem value={e[0]}>{e[1]}</MenuItem>
                    ))}
                  </Select>
                </FormControl>
              </Grid>
            ) : null}
            {["Mortalite"].includes(type) ? (
              <Grid item xs={12}>
                <TextField
                  label="Taux initial"
                  type="number"
                  value={newData.taux_initial}
                  onChange={(e) =>
                    setNewData({ ...newData, taux_initial: e.target.value })
                  }
                />
              </Grid>
            ) : null}
            {["Mortalite"].includes(type) ? (
              <Grid item xs={12}>
                <TextField
                  label="Taux hebdomadaire"
                  type="number"
                  value={newData.taux_hebdomadaire}
                  onChange={(e) =>
                    setNewData({
                      ...newData,
                      taux_hebdomadaire: e.target.value,
                    })
                  }
                />
              </Grid>
            ) : null}
            {/* Nouvelle croissance */}
            {["Croissance"].includes(type) ? (
              <Grid item xs={12}>
                <FormControl style={{ minWidth: "200px" }} required>
                  <InputLabel id="Especie-label">Especie</InputLabel>
                  <Select
                    labelId="Especie-label"
                    id="Especie-select"
                    value={newData.espece_id}
                    onChange={(e) =>
                      setNewData({ ...newData, espece_id: e.target.value })
                    }
                  >
                    {especeList.map((e) => (
                      <MenuItem value={e[0]}>{e[1]}</MenuItem>
                    ))}
                  </Select>
                </FormControl>
              </Grid>
            ) : null}
            {["Croissance"].includes(type) ? (
              <Grid item xs={12}>
                <TextField
                  label="Semaine"
                  type="number"
                  value={newData.semaine}
                  onChange={(e) =>
                    setNewData({ ...newData, semaine: e.target.value })
                  }
                />
              </Grid>
            ) : null}
            {["Croissance"].includes(type) ? (
              <Grid item xs={12}>
                <TextField
                  label="Nombre par livre"
                  type="number"
                  value={newData.nombre_par_livre}
                  onChange={(e) =>
                    setNewData({ ...newData, nombre_par_livre: e.target.value })
                  }
                />
              </Grid>
            ) : null}
            {["Croissance"].includes(type) ? (
              <Grid item xs={12}>
                <TextField
                  label="Pourcentage aliment"
                  type="number"
                  value={newData.pourcentage_aliment}
                  onChange={(e) =>
                    setNewData({
                      ...newData,
                      pourcentage_aliment: e.target.value,
                    })
                  }
                />
              </Grid>
            ) : null}
            {/* <Grid item xs={12}>
              <FormControl style={{ minWidth: "200px" }} required>
                <InputLabel id="Especie-label">Especie del lote</InputLabel>
                <Select
                  labelId="Especie-label"
                  id="Especie-select"
                  value={newLot.espece_id}
                  onChange={(e) =>
                    setNewLot({ ...newLot, espece_id: e.target.value })
                  }
                >
                  {especeList.map((e) => (
                    <MenuItem value={e[0]}>{e[1]}</MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid> */}
          </Grid>
        </DialogContent>
        <DialogActions>
          <Button
            onClick={() => {
              setOpen(false);
            }}
            color="primary"
          >
            Annuler
          </Button>
          <Button onClick={handleConfirm} color="primary">
            Ajouter
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
