import React, { useState } from "react";
import { authFetch } from "auth/index";
import { makeStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import IconButton from "@material-ui/core/IconButton";
import Snackbar from "components/Snackbar/Snackbar.js";
// Icons
import DeleteIcon from "@material-ui/icons/Delete";
import styles from "assets/jss/material-dashboard-react/components/tableStyle.js";

const useStyles = makeStyles(styles);

export default function CustomTableEdit(props) {
  const { tableHead, tableData, tableHeaderColor, postUrl, maj, setMaj } =
    props;
  const classes = useStyles();
  const [tc, setTC] = useState(false);
  const [message, setMessage] = useState("");
  const [color, setColor] = useState("primary");

  const handleSnackbar = (cl, ms) => {
    setColor(cl);
    setMessage(ms);
    setTC(true);
    setTimeout(function () {
      setTC(false);
      setColor("");
      setMessage("");
    }, 4000);
  };

  const onDelete = (id) => {
    const requestOptions = {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ id: id }),
    };
    authFetch(postUrl, requestOptions)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        handleSnackbar("success", response.message);
        setMaj(~maj);
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
  };

  return (
    <div>
      <Snackbar
        place="tc"
        color={color}
        message={message}
        open={tc}
        closeNotification={() => setTC(false)}
        close
      />
      <Paper className={classes.root} style={{ marginTop: "0px" }}>
        <Table
          stickyHeader
          className={classes.table}
          aria-label="caption table"
        >
          <TableHead className={classes[tableHeaderColor + "TableHeader"]}>
            <TableRow
              className={classes.tableHeadRow}
              style={{ height: "40px" }}
            >
              <TableCell></TableCell>
              {tableHead.map((prop) => {
                return prop === "id" ? null : (
                  <TableCell
                    className={classes.tableCell + " " + classes.tableHeadCell}
                    key={prop.id}
                  >
                    {prop}
                  </TableCell>
                );
              })}
            </TableRow>
          </TableHead>
          <TableBody>
            {tableData !== undefined
              ? tableData.map((row) => (
                  <TableRow className={classes.tableRow} key={row.id}>
                    <TableCell className={classes.selectTableCell}>
                      <IconButton
                        style={{ padding: "5px" }}
                        aria-label="delete"
                        onClick={() => onDelete(row.id)}
                      >
                        <DeleteIcon />
                      </IconButton>
                    </TableCell>
                    {tableHead.map((item, key) => {
                      return item === "id" ? null : (
                        <TableCell
                          style={{
                            fontSize: "0.85rem",
                            fontWeight: "300",
                            padding: "5px",
                          }}
                          key={key}
                        >
                          {" "}
                          {row[item]}{" "}
                        </TableCell>
                      );
                    })}
                  </TableRow>
                ))
              : null}
          </TableBody>
        </Table>
      </Paper>
    </div>
  );
}
