import { useState, useEffect } from "react";
import {
  Button,
  CardHeader,
  Card,
  CardContent,
  FormControl,
  FormHelperText,
  Grid,
  InputLabel,
  MenuItem,
  Select,
  TextField,
  Typography,
} from "@material-ui/core";
import CustomTableDelete from "components/TableDelete";
import CardMat from "components/Card/Card.js";
import CardHeaderMat from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import Snackbar from "components/Snackbar/Snackbar.js";
import { authFetch } from "auth";

export default function AddChangementEau() {
  const [changementEau, setChangementEau] = useState({
    bassin: "",
    date: "",
    heure: "",
    pompe: "",
    type: "",
  });
  const [bassinsList, setBassinsList] = useState([""]);
  const [pompesList, setPompesList] = useState([""]);
  const [tc, setTC] = useState(false);
  const [message, setMessage] = useState("");
  const [color, setColor] = useState("primary");
  const [tableHeaders] = useState([
    "id",
    "Bassin",
    "Pompe",
    "Date",
    "Heures",
    "Type",
  ]);
  const [tableData, setTableData] = useState("");
  const [maj, setMaj] = useState(true);

  useEffect(() => {
    var url = "/api/pompes";
    authFetch(url)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        setBassinsList(response.bassins.sort((a, b) => (a[1] > b[1] ? 1 : -1)));
        setPompesList(response.pompes.sort((a, b) => (a[1] > b[1] ? 1 : -1)));
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
    url = "/api/changementEau";
    authFetch(url)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        setTableData(response.changement);
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
  }, [maj]);

  const handleConfirm = () => {
    var url = "/api/pompes";
    const requestOptions = {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        ...changementEau,
        date:
          changementEau.date.substring(6, 10) +
          "-" +
          changementEau.date.substring(3, 5) +
          "-" +
          changementEau.date.substring(0, 2),
      }),
    };
    authFetch(url, requestOptions)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        handleSnackbar("success", response.message);
        setMaj(~maj);
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
    setChangementEau({
      bassin: "",
      date: "",
      heure: "",
      pompe: "",
      type: "",
    });
  };

  const handleSnackbar = (cl, ms) => {
    setColor(cl);
    setMessage(ms);
    setTC(true);
    setTimeout(function () {
      setTC(false);
      setColor("");
      setMessage("");
    }, 4000);
  };

  return (
    <Grid container spacing={1} justify="center" alignItems="center">
      <Snackbar
        place="tc"
        color={color}
        message={message}
        open={tc}
        closeNotification={() => setTC(false)}
        close
      />
      <Grid item>
        <Card>
          <CardHeader
            style={{ textAlign: "center" }}
            title="Añadir un recambio de agua"
          />
          <CardContent>
            <Grid
              item
              container
              spacing={3}
              justify="center"
              alignItems="center"
            >
              <Grid item>
                <FormControl>
                  <InputLabel id="bassin-label">Estanque</InputLabel>
                  <Select
                    labelId="bassin-label"
                    id="bassin-select"
                    value={changementEau.bassin}
                    onChange={(e) =>
                      setChangementEau({
                        ...changementEau,
                        bassin: e.target.value,
                      })
                    }
                  >
                    {bassinsList.map((Bassin, key) => (
                      <MenuItem key={key} value={Bassin[0]}>
                        {Bassin[1]}
                      </MenuItem>
                    ))}
                  </Select>
                  <FormHelperText>Seleccione un estanque</FormHelperText>
                </FormControl>
              </Grid>
              <Grid item>
                <FormControl>
                  <InputLabel id="pompe-label">Bomba</InputLabel>
                  <Select
                    labelId="pompe-label"
                    id="pompe-select"
                    value={changementEau.pompe}
                    onChange={(e) =>
                      setChangementEau({
                        ...changementEau,
                        pompe: e.target.value,
                      })
                    }
                  >
                    {pompesList.map((pompe, key) => (
                      <MenuItem key={key} value={pompe[0]}>
                        {pompe[1]}
                      </MenuItem>
                    ))}
                  </Select>
                  <FormHelperText>Seleccione una bomba</FormHelperText>
                </FormControl>
              </Grid>
              <Grid item>
                <TextField
                  required
                  label="Fecha dd/MM/yyyy"
                  type="text"
                  value={changementEau.date}
                  onChange={(e) =>
                    setChangementEau({ ...changementEau, date: e.target.value })
                  }
                />
              </Grid>
              <Grid item>
                <TextField
                  required
                  label="Hora"
                  value={changementEau.heure}
                  onChange={(e) =>
                    setChangementEau({
                      ...changementEau,
                      heure: e.target.value,
                    })
                  }
                />
              </Grid>
              <Grid item>
                <FormControl style={{ minWidth: "100px" }}>
                  <InputLabel id="type-label">Typo</InputLabel>
                  <Select
                    labelId="type-label"
                    id="type-select"
                    value={changementEau.type}
                    onChange={(e) =>
                      setChangementEau({
                        ...changementEau,
                        type: e.target.value,
                      })
                    }
                  >
                    <MenuItem value="L">L</MenuItem>
                    <MenuItem value="R">R</MenuItem>
                  </Select>
                </FormControl>
              </Grid>
              <Grid item>
                <Button
                  onClick={() => handleConfirm()}
                  variant="contained"
                  color="primary"
                >
                  Confirmar
                </Button>
              </Grid>
            </Grid>
          </CardContent>
        </Card>
        <Grid item>
          {tableHeaders === "" || tableData === "" ? null : (
            <CardMat style={{ marginTop: "50px" }}>
              <CardHeaderMat color="info">
                <Typography variant="h4"> Recambio de agua</Typography>
              </CardHeaderMat>
              <CardBody>
                <CustomTableDelete
                  tableHeaderColor="info"
                  tableHead={tableHeaders}
                  tableData={tableData}
                  postUrl={"/api/changementEau"}
                  maj={maj}
                  setMaj={setMaj}
                />
              </CardBody>
            </CardMat>
          )}
        </Grid>
      </Grid>
    </Grid>
  );
}
