import { useState, useEffect } from "react";
import {
  Button,
  CardHeader,
  Card,
  CardContent,
  Grid,
  TextField,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  makeStyles,
  FormHelperText,
} from "@material-ui/core";
import CustomTableEdit from "components/TableEdit";
import CardMat from "components/Card/Card.js";
import CardBody from "components/Card/CardBody.js";
import Snackbar from "components/Snackbar/Snackbar.js";
import { authFetch } from "auth";

const styles = (theme) => ({
  select: {
    color: "white",
    "&:before": {
      borderColor: "white",
      color: "white",
    },
    "&:after": {
      borderColor: "white",
    },
  },
  icon: {
    fill: "white",
  },
});
const useStyles = makeStyles(styles);

export default function Venta() {
  const [ventePoisson, setVentePoisson] = useState({
    date: "",
    poids: "",
    espece: 1,
  });
  const [venteJour, setVenteJour] = useState("");
  const [filtre, setFiltre] = useState(1);
  const [tc, setTC] = useState(false);
  const [message, setMessage] = useState("");
  const [color, setColor] = useState("primary");
  const [tableHeaders] = useState([
    "fecha",
    "peso",
    "total pesca",
    "% perdida",
  ]);
  const [tableData, setTableData] = useState("");
  const [maj, setMaj] = useState(true);
  const [especesList, setEspecesList] = useState([""]);

  const classes = useStyles();

  useEffect(() => {
    var url = "/api/especes/poissons";
    authFetch(url)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        setEspecesList(response.especes);
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
  }, []);

  useEffect(() => {
    var url = "/api/ventes/poissons/" + filtre;
    authFetch(url)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        setTableData(response.ventes);
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
  }, [maj, filtre]);

  const handleConfirm = () => {
    var url = "/api/ventes/poissons";
    const requestOptions = {
      method: "PUT",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        ...ventePoisson,
        date:
          ventePoisson.date.substring(6, 10) +
          "-" +
          ventePoisson.date.substring(3, 5) +
          "-" +
          ventePoisson.date.substring(0, 2),
      }),
    };
    authFetch(url, requestOptions)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        handleSnackbar("success", response.message);
        setMaj(~maj);
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
  };

  const handleSnackbar = (cl, ms) => {
    setColor(cl);
    setMessage(ms);
    setTC(true);
    setTimeout(function () {
      setTC(false);
      setColor("");
      setMessage("");
    }, 4000);
  };

  // useEffect(() => {
  //   console.log(venteJour, ventePoisson.date.length);
  // }, [venteJour, ventePoisson]);

  const handleChangeDate = (d, e) => {
    console.log(d, d.lenght);
    let f = e !== "" ? e : filtre;
    if (d.length === 10) {
      var url =
        "/api/ventes/poissonsJour/" +
        f +
        "/" +
        d.substring(6) +
        "-" +
        d.substring(3, 5) +
        "-" +
        d.substring(0, 2);
      authFetch(url)
        .then((response) => {
          if (response.ok) {
            return response.json();
          } else {
            throw new Error(response.statusText);
          }
        })
        .then((response) => {
          setVenteJour(response.total);
        })
        .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
    }
  };

  return (
    <Grid container xs={12} spacing={1} justify="center" alignItems="center">
      <Snackbar
        place="tc"
        color={color}
        message={message}
        open={tc}
        closeNotification={() => setTC(false)}
        close
      />
      <Grid
        item
        xs={12}
        container
        spacing={1}
        justify="center"
        alignItems="center"
      >
        <Grid item lg={5}>
          <Card>
            <CardHeader
              style={{ textAlign: "center" }}
              title="Añadir una venta de pescados"
            />
            <CardContent>
              <Grid
                item
                container
                spacing={3}
                justify="center"
                alignItems="center"
              >
                <Grid item>
                  <FormControl style={{ minWidth: "150px" }}>
                    <InputLabel id="espece-label">Espece</InputLabel>
                    <Select
                      labelId="espece-label"
                      id="espece-select"
                      value={ventePoisson.espece}
                      onChange={(e) => {
                        setVentePoisson({
                          ...ventePoisson,
                          espece: parseInt(e.target.value),
                        });
                        setVenteJour("");
                        setFiltre(parseInt(e.target.value));
                        handleChangeDate(
                          ventePoisson.date,
                          parseInt(e.target.value)
                        );
                      }}
                    >
                      {especesList.map((espece, key) => (
                        <MenuItem key={key} value={espece[0]}>
                          {espece[1]}
                        </MenuItem>
                      ))}
                    </Select>
                  </FormControl>
                </Grid>
                <Grid item>
                  <TextField
                    required
                    label="Fecha dd/MM/yyyy"
                    type="text"
                    value={ventePoisson.date}
                    onChange={(e) => {
                      setVentePoisson({
                        ...ventePoisson,
                        date: e.target.value,
                      });
                      handleChangeDate(e.target.value, "");
                    }}
                  />
                </Grid>
                <Grid item>
                  <TextField
                    required
                    type="number"
                    label="Peso total"
                    value={ventePoisson.poids}
                    onChange={(e) =>
                      setVentePoisson({
                        ...ventePoisson,
                        poids: e.target.value,
                      })
                    }
                  />
                </Grid>
                <Grid item>
                  {venteJour !== "" ? (
                    <p>
                      Peso total pesca: <b>{venteJour}</b>
                    </p>
                  ) : null}
                </Grid>
                <Grid item>
                  <Button
                    onClick={() => handleConfirm()}
                    variant="contained"
                    color="primary"
                  >
                    Confirmar
                  </Button>
                </Grid>
              </Grid>
            </CardContent>
          </Card>
        </Grid>
        <Grid item xs={12}>
          {tableHeaders === "" || tableData === "" ? null : (
            <CardMat style={{ marginTop: "50px" }}>
              <CardHeader
                style={{
                  color: "white",
                  backgroundColor: "#3f51b5",
                  fontFamily: '"Roboto", "Helvetica", "Arial", sans-serif',
                  fontSize: "34px",
                  fontWeight: 400,
                  lineHeight: 1.235,
                }}
                color="info"
                title="Venta de pescados"
                action={
                  <FormControl
                    style={{
                      backgroundColor: "#f50057",
                      color: "fff",
                      boxShadow:
                        "0px 3px 1px -2px rgb(0 0 0 / 20%), 0px 2px 2px 0px rgb(0 0 0 / 14%), 0px 1px 5px 0px rgb(0 0 0 / 12%)",
                      borderRadius: "4px",
                      marginRight: "8px",
                      padding: 8,
                    }}
                  >
                    <Select
                      className={classes.select}
                      inputProps={{
                        classes: {
                          icon: classes.icon,
                        },
                      }}
                      labelId="Especie-label"
                      id="Especie-select"
                      value={filtre}
                      onChange={(e) => setFiltre(e.target.value)}
                    >
                      {especesList.map((espece, key) => (
                        <MenuItem key={key} value={espece[0]}>
                          {espece[1]}
                        </MenuItem>
                      ))}
                    </Select>
                    <FormHelperText style={{ color: "white" }}>
                      Filtrar por especies
                    </FormHelperText>
                  </FormControl>
                }
              ></CardHeader>
              <CardBody>
                <CustomTableEdit
                  tableHeaderColor="info"
                  tableHead={tableHeaders}
                  tableData={tableData}
                  postUrl={"/api/ventes/poissons"}
                />
              </CardBody>
            </CardMat>
          )}
        </Grid>
      </Grid>
    </Grid>
  );
}
