import React, { useState } from "react";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import { authFetch } from "auth";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import { Grid } from "@material-ui/core";
import Snackbar from "components/Snackbar/Snackbar.js";

export default function AddAlimentDialog({ setMaj, maj }) {
  const [open, setOpen] = useState(false);
  const [newAliment, setNewAliment] = useState({
    libelle: "",
  });

  const [tc, setTC] = useState(false);
  const [message, setMessage] = useState("");
  const [color, setColor] = useState("primary");

  const handleConfirm = () => {
    var url = "/api/aliments";
    const requestOptions = {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        ...newAliment,
      }),
    };
    authFetch(url, requestOptions)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        handleSnackbar("success", response.message);
        setMaj(~maj);
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
    setOpen(false);
    setNewAliment({
      libelle: "",
    });
  };

  const handleSnackbar = (cl, ms) => {
    setColor(cl);
    setMessage(ms);
    setTC(true);
    setTimeout(function () {
      setTC(false);
      setColor("");
      setMessage("");
    }, 4000);
  };

  return (
    <div>
      <Snackbar
        place="tc"
        color={color}
        message={message}
        open={tc}
        closeNotification={() => setTC(false)}
        close
      />
      <Button
        id="ButtonDialog"
        variant="contained"
        color="primary"
        onClick={() => setOpen(true)}
      >
        Añadir un typo de alimento
      </Button>
      <Dialog
        open={open}
        onClose={() => {
          setOpen(false);
        }}
        aria-labelledby="form-dialog-title"
      >
        <DialogTitle id="form-dialog-title">
          {" "}
          Añadir un typo de alimento
        </DialogTitle>
        <DialogContent>
          <Grid container spacing={1} justify="center" alignItems="center">
            <Grid item xs={12}>
              <TextField
                label="Nombre"
                type="text"
                value={newAliment.libelle}
                onChange={(e) =>
                  setNewAliment({ ...newAliment, libelle: e.target.value })
                }
              />
            </Grid>
          </Grid>
        </DialogContent>
        <DialogActions>
          <Button
            onClick={() => {
              setOpen(false);
            }}
            color="primary"
          >
            Annuler
          </Button>
          <Button onClick={handleConfirm} color="primary">
            Ajouter
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
