import React, { useState } from "react";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import { authFetch } from "auth";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import { Grid } from "@material-ui/core";
import Snackbar from "components/Snackbar/Snackbar.js";

export default function AddClientDialog({ setMaj, maj }) {
  const [open, setOpen] = useState(false);
  const [newClient, setNewClient] = useState({
    libelle: "",
    adresse: "",
    cedula: "",
  });

  const [tc, setTC] = useState(false);
  const [message, setMessage] = useState("");
  const [color, setColor] = useState("primary");

  const handleConfirm = () => {
    var url = "/api/clients";
    const requestOptions = {
      method: "PUT",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        ...newClient,
        cedula: parseInt(newClient.cedula),
      }),
    };
    authFetch(url, requestOptions)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        handleSnackbar("success", response.message);
        setMaj(~maj);
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
    setOpen(false);
    setNewClient({
      libelle: "",
      adresse: "",
      cedula: "",
    });
  };

  const handleSnackbar = (cl, ms) => {
    setColor(cl);
    setMessage(ms);
    setTC(true);
    setTimeout(function () {
      setTC(false);
      setColor("");
      setMessage("");
    }, 4000);
  };

  return (
    <div>
      <Snackbar
        place="tc"
        color={color}
        message={message}
        open={tc}
        closeNotification={() => setTC(false)}
        close
      />
      <Button
        id="ButtonDialog"
        variant="contained"
        color="primary"
        onClick={() => setOpen(true)}
      >
        Añadir un cliente
      </Button>
      <Dialog
        open={open}
        onClose={() => {
          setOpen(false);
        }}
        aria-labelledby="form-dialog-title"
      >
        <DialogTitle id="form-dialog-title"> Añadir un cliente</DialogTitle>
        <DialogContent>
          <Grid container spacing={1} justify="center" alignItems="center">
            <Grid item xs={12}>
              <TextField
                label="Nombre"
                type="text"
                value={newClient.libelle}
                onChange={(e) =>
                  setNewClient({ ...newClient, libelle: e.target.value })
                }
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                label="Direcion"
                type="text"
                value={newClient.adresse}
                onChange={(e) =>
                  setNewClient({ ...newClient, adresse: e.target.value })
                }
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                label="Cedula"
                type="text"
                value={newClient.cedula}
                onChange={(e) =>
                  setNewClient({ ...newClient, cedula: e.target.value })
                }
              />
            </Grid>
          </Grid>
        </DialogContent>
        <DialogActions>
          <Button
            onClick={() => {
              setOpen(false);
            }}
            color="primary"
          >
            Annuler
          </Button>
          <Button onClick={handleConfirm} color="primary">
            Ajouter
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
