import { useState, useEffect } from "react";
import {
  CardHeader,
  Card,
  CardContent,
  FormControl,
  Grid,
  InputLabel,
  MenuItem,
  Select,
  Typography,
  TextField,
  Button,
} from "@material-ui/core";
import Table from "components/TableDelete";
import CardMat from "components/Card/Card.js";
import CardHeaderMat from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import Snackbar from "components/Snackbar/Snackbar.js";
import { authFetch } from "auth";
import AddClientDialog from "components/Dialog/AddClientDialog";

export default function Clients({ clientsList, maj, setMaj }) {
  const [client, setClient] = useState({
    id: clientsList[0][0],
  });
  const [inf, setInf] = useState({ adresse: "", cedula: "" });
  const [tableHeaders] = useState([
    "Type aliments",
    "Date",
    "Quantite",
    "Commentaire",
  ]);
  const [tableData, setTableData] = useState("");

  const [tc, setTC] = useState(false);
  const [message, setMessage] = useState("");
  const [color, setColor] = useState("primary");

  useEffect(() => {
    const url = "/api/clients/" + client.id;
    authFetch(url)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        setInf({ adresse: response.adresse, cedula: response.cedula });
        setTableData(response.achats);
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
  }, [client, maj]);

  const handleSnackbar = (cl, ms) => {
    setColor(cl);
    setMessage(ms);
    setTC(true);
    setTimeout(function () {
      setTC(false);
      setMessage("");
    }, 4000);
  };
  const handleConfirm = (cl, ms) => {
    var url = "/api/clients";
    const requestOptions = {
      method: "PUT",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        ...client,
        ...inf,
      }),
    };
    authFetch(url, requestOptions)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        handleSnackbar("success", response.message);
        setMaj(~maj);
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
  };

  return (
    <Grid container spacing={1} justify="center" alignItems="center">
      <Snackbar
        place="tc"
        color={color}
        message={message}
        open={tc}
        closeNotification={() => setTC(false)}
        close
      />
      <Grid item>
        <Card>
          <CardHeader
            title="Gestion des clients"
            action={<AddClientDialog maj={maj} setMaj={setMaj} />}
          />
          <CardContent>
            <Grid container spacing={1} justify="center" alignItems="center">
              <Grid
                item
                container
                spacing={1}
                justify="center"
                alignItems="center"
                xs={12}
              >
                <Grid item>
                  <FormControl style={{ minWidth: "150px" }}>
                    <InputLabel id="Client-label">Client</InputLabel>
                    <Select
                      labelId="Client-label"
                      id="Client-select"
                      value={client.id}
                      onChange={(e) =>
                        setClient({
                          ...client,
                          id: e.target.value,
                        })
                      }
                    >
                      {clientsList.map((c, key) => (
                        <MenuItem key={key} value={c[0]}>
                          {c[1]}
                        </MenuItem>
                      ))}
                    </Select>
                  </FormControl>
                </Grid>
                <Grid item>
                  <TextField
                    label="Direcion"
                    type="text"
                    InputLabelProps={{ shrink: true }}
                    value={inf.adresse}
                    onChange={(e) =>
                      setInf({
                        ...inf,
                        adresse: e.target.value,
                      })
                    }
                  />
                </Grid>
                <Grid item>
                  <TextField
                    label="Cedula"
                    type="text"
                    InputLabelProps={{ shrink: true }}
                    value={inf.cedula}
                    onChange={(e) =>
                      setInf({
                        ...inf,
                        cedula: e.target.value,
                      })
                    }
                  />
                </Grid>
                <Button
                  onClick={handleConfirm}
                  variant="contained"
                  color="primary"
                >
                  Cambiar
                </Button>
              </Grid>
              <Grid item xs={6}>
                {tableHeaders === "" || tableData === "" ? null : (
                  <CardMat style={{ marginTop: "50px" }}>
                    <CardHeaderMat color="info">
                      <Typography variant="h4">
                        Compra de {client.libelle}
                      </Typography>
                    </CardHeaderMat>
                    <CardBody>
                      <Table
                        tableHeaderColor="info"
                        tableHead={tableHeaders}
                        tableData={tableData}
                        postUrl={"/api/clients"}
                        maj={maj}
                        setMaj={setMaj}
                      />
                    </CardBody>
                  </CardMat>
                )}
              </Grid>
              <Grid item xs={6}>
                <Card>
                  <CardHeaderMat style={{ marginTop: "1em" }} color="primary">
                    Stats dans le futur
                  </CardHeaderMat>
                  <CardBody>
                    <Grid
                      container
                      spacing={1}
                      justify="center"
                      alignItems="center"
                    >
                      <Grid item>
                        <h4>En moyenne commande toute les x semaines</h4>
                      </Grid>
                    </Grid>
                  </CardBody>
                </Card>
              </Grid>
            </Grid>
          </CardContent>
        </Card>
      </Grid>
    </Grid>
  );
}
