import React, { useState, useEffect } from "react";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import { authFetch } from "auth";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import {
  FormControl,
  Grid,
  InputLabel,
  MenuItem,
  Select,
} from "@material-ui/core";

export default function FormDialog({ cycle, handleSnackbar, setMaj, maj }) {
  const [open, setOpen] = useState(false);
  const [newLot, setNewLot] = useState({
    espece_id: "",
    cycle_id: cycle,
    commentaire: "",
  });
  const [especeList, setEspeceList] = useState([]);

  useEffect(() => {
    var url = "/api/especes";
    authFetch(url)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        setEspeceList(response.especes);
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
  }, [handleSnackbar]);

  const handleConfirm = () => {
    var url = "/api/lots";
    const requestOptions = {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ ...newLot }),
    };
    authFetch(url, requestOptions)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        handleSnackbar("success", response.message);
        setMaj(~maj);
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
    setOpen(false);
    setNewLot({
      espece_id: "",
      cycle_id: cycle,
      commentaire: "",
    });
  };

  return (
    <div>
      <Button
        id="ButtonDialog"
        variant="contained"
        color="primary"
        onClick={() => setOpen(true)}
      >
        Añadir un lote
      </Button>
      <Dialog
        open={open}
        onClose={() => {
          setOpen(false);
        }}
        aria-labelledby="form-dialog-title"
      >
        <DialogTitle id="form-dialog-title">Añadir un lote</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Elija la especie y un commentario.
          </DialogContentText>
          <Grid container spacing={1} justify="center" alignItems="center">
            <Grid item xs={12}>
              <FormControl style={{ minWidth: "200px" }} required>
                <InputLabel id="Especie-label">Especie del lote</InputLabel>
                <Select
                  labelId="Especie-label"
                  id="Especie-select"
                  value={newLot.espece_id}
                  onChange={(e) =>
                    setNewLot({ ...newLot, espece_id: e.target.value })
                  }
                >
                  {especeList.map((e) => (
                    <MenuItem value={e[0]}>{e[1]}</MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>
            <Grid item xs={12}>
              <TextField
                label="Comentario"
                type="text"
                value={newLot.commentaire}
                onChange={(e) =>
                  setNewLot({ ...newLot, commentaire: e.target.value })
                }
              />
            </Grid>
          </Grid>
        </DialogContent>
        <DialogActions>
          <Button
            onClick={() => {
              setOpen(false);
            }}
            color="primary"
          >
            Annuler
          </Button>
          <Button onClick={handleConfirm} color="primary">
            Ajouter
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
