import { useState, useEffect } from "react";
import {
  Button,
  CardHeader,
  Card,
  CardContent,
  FormControl,
  Grid,
  InputLabel,
  MenuItem,
  Select,
  TextField,
} from "@material-ui/core";
import {
  Page,
  View,
  Document,
  Line,
  StyleSheet,
  Svg,
  Rect,
  Text,
  G,
} from "@react-pdf/renderer";
import Snackbar from "components/Snackbar/Snackbar.js";
import { authFetch } from "auth";
import Clients from "../components/Clients";
import VentesParJour from "../components/VentesParJour";
import AddClientDialog from "components/Dialog/AddClientDialog";
import AddAlimentDialog from "components/Dialog/AddAlimentDialog";
import Commandes from "components/Commandes";
import { PDFDownloadLink } from "@react-pdf/renderer";

export default function VenteAliment() {
  const [newVente, setNewVente] = useState({
    id: 0,
    typeId: 2,
    Typo: "",
    date: "",
    clientId: 1,
    Lbs: "",
    nbrSac: "",
    poidsSac: "",
    Precio: "",
    "Precio Total": "",
    commentaire: "",
  });
  const [clientsList, setClientsList] = useState([]);
  const [typeAlimentList, setTypeAlimentList] = useState([]);
  const [maj, setMaj] = useState(true);
  const [majC, setMajC] = useState(true);
  const [commandes, setCommandes] = useState([]);

  const [tc, setTC] = useState(false);
  const [message, setMessage] = useState("");
  const [color, setColor] = useState("primary");

  useEffect(() => {
    console.log(commandes);
  }, [commandes]);

  useEffect(() => {
    var url = "/api/ventes/aliments";
    authFetch(url)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        setTypeAlimentList(
          response.typeAliment.sort((a, b) => (a[1] > b[1] ? 1 : -1))
        );
        const l = response.clients;
        setClientsList(l);
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
  }, [maj]);

  const handleAddCommande = () => {
    let temp = newVente;
    temp.Typo = typeAlimentList.find((e) => e[0] === newVente.typeId)[1];
    if (temp.Lbs === "") {
      temp.Lbs = temp.nbrSac * temp.poidsSac;
      temp["Precio Total"] = temp.Precio * temp.nbrSac;
    }
    if (temp.nbrSac === "") {
      if (temp.poidsSac !== "") {
        temp.nbrSac = Math.ceil(temp.Lbs / temp.poidsSac);
      }
      temp["Precio Total"] = temp.Precio * temp.Lbs;
    }
    temp["Cantidad de sacos"] = temp.nbrSac;
    setCommandes(commandes.concat([temp]));
    setNewVente({
      ...newVente,
      typeId: 2,
      Typo: "",
      Lbs: "",
      nbrSac: "",
      poidsSac: "",
      Precio: "",
      "Precio Total": "",
      commentaire: "",
      id: newVente.id + 1,
    });
  };

  const handleConfirm = () => {
    var url = "/api/ventes/aliments";
    const requestOptions = {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        commandes,
        date:
          newVente.date.substring(6, 10) +
          "-" +
          newVente.date.substring(3, 5) +
          "-" +
          newVente.date.substring(0, 2),
      }),
    };
    authFetch(url, requestOptions)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        handleSnackbar("success", response.message);
        setMaj(~maj);
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
    setNewVente({
      id: 0,
      typeId: 2,
      Typo: "",
      date: "",
      clientId: 1,
      Lbs: "",
      nbrSac: "",
      poidsSac: "",
      Precio: "",
      "Precio Total": "",
      commentaire: "",
    });
    setCommandes([]);
  };

  const handleSnackbar = (cl, ms) => {
    setColor(cl);
    setMessage(ms);
    setTC(true);
    setTimeout(function () {
      setTC(false);
      setColor("");
      setMessage("");
    }, 4000);
  };

  return (
    <Grid container spacing={1} justify="center" alignItems="center">
      <Snackbar
        place="tc"
        color={color}
        message={message}
        open={tc}
        closeNotification={() => setTC(false)}
        close
      />
      <Grid item>
        <Card>
          <CardHeader
            title="Añadir una venta"
            action={
              clientsList.length === 0 ? (
                <AddClientDialog maj={maj} setMaj={setMaj} />
              ) : (
                <AddAlimentDialog maj={maj} setMaj={setMaj} />
              )
            }
          />
          <CardContent>
            <Grid container spacing={1} justify="center" alignItems="center">
              <Grid
                container
                item
                spacing={1}
                justify="center"
                alignItems="center"
              >
                <Grid item>
                  <FormControl style={{ minWidth: "150px" }}>
                    <InputLabel id="Client-label">Client</InputLabel>
                    <Select
                      labelId="Client-label"
                      id="Client-select"
                      value={newVente.clientId}
                      onChange={(e) =>
                        setNewVente({
                          ...newVente,
                          clientId: e.target.value,
                        })
                      }
                    >
                      {clientsList
                        .sort((a, b) => a[1].localeCompare(b[1]))
                        .map((c, key) => (
                          <MenuItem key={key} value={c[0]}>
                            {c[1]}
                          </MenuItem>
                        ))}
                    </Select>
                  </FormControl>
                </Grid>
                <Grid item>
                  <TextField
                    required
                    label="Fecha dd/MM/yyyy"
                    type="text"
                    value={newVente.date}
                    onChange={(e) =>
                      setNewVente({ ...newVente, date: e.target.value })
                    }
                  />
                </Grid>
                <Grid item>
                  {commandes.length !== 0 ? (
                    <PDFDownloadLink
                      document={
                        <MyDocument
                          client={
                            clientsList.find(
                              (e) => e[0] === newVente.clientId
                            )[1]
                          }
                          adresse={
                            clientsList.find(
                              (e) => e[0] === newVente.clientId
                            )[2]
                          }
                          date={newVente.date}
                          listeCommande={commandes.map((c) => {
                            return {
                              aliment: c.Typo,
                              quantite: c.Lbs,
                              prix: c.Precio,
                              prixTotal: c["Precio Total"],
                              "Cantidad de sacos": c["Cantidad de sacos"],
                            };
                          })}
                        />
                      }
                      fileName="facture.pdf"
                    >
                      {({ blob, url, loading, error }) =>
                        loading ? (
                          "Loading document..."
                        ) : (
                          <Button variant="contained" color="secondary">
                            Factura
                          </Button>
                        )
                      }
                    </PDFDownloadLink>
                  ) : (
                    ""
                  )}
                </Grid>
                <Grid item>
                  <Button
                    onClick={handleConfirm}
                    variant="contained"
                    color="primary"
                  >
                    Confirmar
                  </Button>
                </Grid>
              </Grid>
              <Grid item>
                <FormControl style={{ minWidth: "150px" }}>
                  <InputLabel id="alimento-label">Typo de alimento</InputLabel>
                  <Select
                    labelId="alimento-label"
                    id="alimento-select"
                    value={newVente.typeId}
                    onChange={(e) =>
                      setNewVente({
                        ...newVente,
                        typeId: e.target.value,
                      })
                    }
                  >
                    {typeAlimentList
                      .sort((a, b) => a[1].localeCompare(b[1]))
                      .map((c, key) => (
                        <MenuItem key={key} value={c[0]}>
                          {c[1]}
                        </MenuItem>
                      ))}
                  </Select>
                </FormControl>
              </Grid>
              <Grid item>
                <TextField
                  required
                  label="Lbs"
                  type="text"
                  value={newVente.Lbs}
                  onChange={(e) =>
                    setNewVente({
                      ...newVente,
                      Lbs: e.target.value,
                    })
                  }
                />
              </Grid>
              <Grid item>
                <TextField
                  required
                  label="Cantidad de sacos"
                  type="text"
                  value={newVente.nbrSac}
                  onChange={(e) =>
                    setNewVente({
                      ...newVente,
                      nbrSac: e.target.value,
                    })
                  }
                />
              </Grid>
              <Grid item>
                <TextField
                  required
                  label="Peso saco"
                  type="text"
                  value={newVente.poidsSac}
                  onChange={(e) =>
                    setNewVente({
                      ...newVente,
                      poidsSac: e.target.value,
                    })
                  }
                />
              </Grid>
              <Grid item>
                <TextField
                  label="Precio"
                  type="text"
                  value={newVente.Precio}
                  onChange={(e) =>
                    setNewVente({
                      ...newVente,
                      Precio: e.target.value,
                    })
                  }
                />
              </Grid>
              <Grid item>
                <Button
                  onClick={handleAddCommande}
                  variant="contained"
                  color="primary"
                >
                  Añadir
                </Button>
              </Grid>
              <Grid
                item
                xs={12}
                style={{ marginLeft: "50px", marginRight: "50px" }}
              >
                <Commandes
                  tableHead={[
                    "Typo",
                    "Cantidad de sacos",
                    "Lbs",
                    "Precio",
                    "Precio Total",
                  ]}
                  tableData={commandes}
                  tableHeaderColor={"info"}
                  maj={majC}
                  setMaj={setMajC}
                  setCommandes={setCommandes}
                />
              </Grid>
            </Grid>
          </CardContent>
        </Card>
      </Grid>
      <Grid item xs={12}>
        {clientsList.length !== 0 ? (
          <Clients
            clientsList={clientsList.sort((a, b) => a[1].localeCompare(b[1]))}
            maj={maj}
            setMaj={setMaj}
          ></Clients>
        ) : null}
      </Grid>
      <Grid item xs={12}>
        <VentesParJour maj={maj} setMaj={setMaj} />
      </Grid>
    </Grid>
  );
}

// Create styles
const styles = StyleSheet.create({
  page: {
    flexDirection: "column",
    backgroundColor: "#E4E4E4",
  },
  section: {
    margin: 0,
    padding: 0,
    flexGrow: 1,
  },
});

