import React, { useState, useEffect } from "react";
import { authFetch } from "auth";
import {
  Grid,
  Button,
  TextField,
  CardHeader,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  FormControlLabel,
  Checkbox,
  CircularProgress,
} from "@material-ui/core";
import Card from "components/Card/Card.js";
import CardBody from "components/Card/CardBody.js";
import Snackbar from "components/Snackbar/Snackbar.js";
import ConfirmChangeDialog from "components/Dialog/ConfirmChangeDialog";
import { makeStyles } from "@material-ui/core/styles";
import styles from "assets/jss/material-dashboard-react/components/cardHeaderStyle.js";
import CsvDownload from "react-json-to-csv";

const useStyles = makeStyles(styles);

export default function Alimentation() {
  const [tc, setTC] = useState(false);
  const [message, setMessage] = useState("");
  const [color, setColor] = useState("primary");
  const [loading, setLoading] = useState(true);
  const [fetchDate, setFetchDate] = useState("");
  const [closed, setClosed] = useState(false);
  const [aliData, setAliData] = useState([]);
  const [alimentList, setAlimentList] = useState([]);
  const [cyclesList, setCyclesList] = useState(undefined);
  const [cycle, setCycle] = useState(undefined);
  const [maj, setMaj] = useState(false);
  const [poidsAM, setPoidsAM] = useState("");
  const [poidsPM, setPoidsPM] = useState("");
  const [date, setDate] = useState(new Date().toISOString().substring(0, 10));
  const [total, setTotal] = useState("");

  const classes = useStyles();

  useEffect(() => {
    setLoading(true);
  }, [date]);

  useEffect(() => {
    var url = "/api/alimentation/" + date + "/" + closed;
    authFetch(url)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        setCyclesList(
          response.cycles.sort((a, b) => (a.Bassin > b.Bassin ? 1 : -1))
        );
        setAliData(
          transformData(
            response.cycles.sort((a, b) => (a.Bassin > b.Bassin ? 1 : -1)),
            response.aliments
          )
        );
        setAlimentList(response.aliments);
        setFetchDate(response.date);
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
  }, [date, closed]);

  useEffect(() => {
    var url = "/api/alimentationTotal/" + date;
    authFetch(url)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        setTotal(response.total);
        setMaj(false);
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
  }, [date, maj]);

  useEffect(() => {
    if (cycle && cycle.ali && maj) {
      if (cycle.Poids_pm) {
        setPoidsAM(cycle.Poids - cycle.Poids_pm);
        setPoidsPM(cycle.Poids_pm);
      } else {
        setPoidsAM(cycle.Poids);
        setPoidsPM("");
      }
    } else if (cycle && maj) {
      setPoidsAM("");
      setPoidsPM("");
    }
    setMaj(false);
  }, [cycle, maj]);

  useEffect(() => {
    if (~loading) {
      if (fetchDate !== date) {
        setDate(date);
        setLoading(true);
      } else {
        setLoading(false);
      }
    }
  }, [loading, date, fetchDate]);

  const handleSnackbar = (cl, ms) => {
    setColor(cl);
    setMessage(ms);
    setTC(true);
    setTimeout(function () {
      setTC(false);
      setColor("");
      setMessage("");
    }, 4000);
  };

  const changeDate = (d) => {
    var result = new Date(date + "T12:00:00");
    result.setDate(result.getDate() + d);
    setDate(result.toISOString().substring(0, 10));
    setCycle(undefined);
  };

  const handleChangeCycle = (id) => {
    setCycle({
      ...cyclesList[id],
      id: id,
      Type_Aliment: cyclesList[id].Type_Aliment
        ? cyclesList[id].Type_Aliment
        : "",
    });
    setMaj(true);
  };

  const transformData = (c, aliments) => {
    if (c !== undefined) {
      let temp = [];
      c.forEach((data) => {
        let ali = aliments.find((a) => a[0] === data.Type_Aliment);
        if (ali === undefined) {
          temp.push({
            Estanque: data.Bassin,
            Alimento: 0,
            Cantidad: 0,
          });
        } else {
          temp.push({
            Estanque: data.Bassin,
            Alimento: ali[1].replace('"', " "),
            Cantidad: data["poids aliment a donner"],
          });
        }
      });
      return temp;
    }
  };

  const resetStockAli = () => {
    var url = "/api/resetStockAlimentation/" + date;
    authFetch(url)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        handleSnackbar("success", response.message);
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
  };

  return (
    <div>
      <Snackbar
        place="tc"
        color={color}
        message={message}
        open={tc}
        closeNotification={() => setTC(false)}
        close
      />
      <Grid container spacing={2}>
        <Grid style={{ padding: "0" }} item xs={12} container justify="center">
          <Button
            onClick={() => changeDate(-1)}
            variant="contained"
            color="primary"
          >
            {"<<"}
          </Button>
          <Button
            style={{ marginLeft: "5px" }}
            variant="contained"
            color="primary"
          >
            {date.split("-").reverse().join("-")}
          </Button>
          <Button
            style={{ marginLeft: "5px" }}
            onClick={() => changeDate(1)}
            variant="contained"
            color="primary"
          >
            {">>"}
          </Button>
          <Button
            style={{ marginLeft: "5px" }}
            onClick={() => resetStockAli()}
            variant="contained"
            color="secondary"
          >
            {"Reset Stock Alimentation"}
          </Button>
          <CsvDownload
            data={aliData}
            filename={"alimentacion-" + date + ".csv"}
            style={{
              backgroundColor: "#3f51b5",
              borderRadius: "4px",
              borderWidth: "0",
              marginLeft: "5px",
              display: "inline-block",
              cursor: "pointer",
              color: "#ffffff",
              fontSize: "14px",
              fontWeight: "500",
              font: "Roboto",
              lineHeight: "1.75",
              padding: "6px 24px",
              textDecoration: "none",
            }}
          >
            Descargar alimentacion
          </CsvDownload>
          <FormControlLabel
            control={
              <Checkbox
                checked={closed}
                onChange={(e) => setClosed(e.target.checked)}
              />
            }
            label="Ver cerrados"
          />
        </Grid>
        <Grid
          container
          spacing={1}
          item
          xs={4}
          style={{ overflow: "scroll", height: "100vh" }}
        >
          {cyclesList &&
            cyclesList.map((c, key) => (
              <Grid item xs={12} key={key}>
                <Button
                  style={{
                    height: "65px",
                    color: "white",
                    backgroundColor: c.ali ? "#4caf50" : "#3f51b5",
                  }}
                  id={key}
                  variant="contained"
                  fullWidth
                  onClick={(e) => {
                    e.target.id === ""
                      ? handleChangeCycle(e.target.offsetParent.id)
                      : handleChangeCycle(e.target.id);
                  }}
                >
                  {c.Bassin}
                </Button>
              </Grid>
            ))}
        </Grid>
        <Grid item xs={8}>
          {cycle && ~loading ? (
            <Card>
              <CardHeader
                className={classes.cardHeader}
                title={cycle.Bassin}
                titleTypographyProps={{ variant: "h2", align: "center" }}
                style={{
                  backgroundColor: "#009688",
                  padding: "20px",
                  color: "white",
                  fontSize: "2rem",
                }}
              ></CardHeader>
              <CardBody>
                <Grid container spacing={2} alignItems="center">
                  <Grid item xs={2}>
                    <Card style={{ marginBottom: "10px" }}>
                      <h4 style={{ textAlign: "center" }}>
                        Peso teorico: {cycle["poids aliment a donner"]}
                      </h4>
                    </Card>
                  </Grid>
                  <Grid item xs={2} style={{ textAlign: "center" }}>
                    <TextField
                      variant="outlined"
                      label="Peso por la mañana"
                      value={poidsAM}
                      onChange={(e) =>
                        setPoidsAM(
                          e.target.value === "" ? "" : parseInt(e.target.value)
                        )
                      }
                    ></TextField>
                  </Grid>
                  <Grid item xs={2} style={{ textAlign: "center" }}>
                    <TextField
                      variant="outlined"
                      label="Peso por la tarde"
                      value={poidsPM}
                      onChange={(e) =>
                        setPoidsPM(
                          e.target.value === "" ? "" : parseInt(e.target.value)
                        )
                      }
                    ></TextField>
                  </Grid>
                  <Grid item xs={3}>
                    <FormControl style={{ minWidth: "100px" }}>
                      <InputLabel id="type-label">Alimento</InputLabel>
                      <Select
                        labelId="type-label"
                        id="type-select"
                        value={cycle.Type_Aliment}
                        onChange={(e) =>
                          setCycle({ ...cycle, Type_Aliment: e.target.value })
                        }
                      >
                        {alimentList !== []
                          ? alimentList.map((a, key) => {
                              return (
                                <MenuItem key={key} value={a[0]}>
                                  {a[1]}
                                </MenuItem>
                              );
                            })
                          : null}
                      </Select>
                    </FormControl>
                  </Grid>
                  <Grid item xs={2}>
                    <ConfirmChangeDialog
                      cycle={cycle}
                      cyclesList={cyclesList}
                      setCyclesList={setCyclesList}
                      setCycle={setCycle}
                      poidsAM={poidsAM}
                      poidsPM={poidsPM}
                      date={date}
                      handleSnackbar={handleSnackbar}
                      setMaj={setMaj}
                      maj={maj}
                    />
                  </Grid>
                </Grid>
              </CardBody>
            </Card>
          ) : loading ? (
            <Grid
              style={{ height: "80%" }}
              container
              spacing={2}
              alignItems="center"
              justify="center"
            >
              <Grid item>
                <CircularProgress
                  style={{
                    display: "flex",
                    height: "150px",
                    width: "150px",
                  }}
                />
              </Grid>
            </Grid>
          ) : null}
        </Grid>
        <Grid item xs={12}>
          <Card>
            <CardBody>
              <div style={{ textAlign: "center" }}>
                {total
                  ? total.map((t) => {
                      return (
                        <>
                          <b>{t[0]}</b> : {t[1]} lbs |{" "}
                        </>
                      );
                    })
                  : null}
              </div>
            </CardBody>
          </Card>
        </Grid>
      </Grid>
    </div>
  );
}
