import React, { useState } from "react";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import { authFetch } from "auth";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import {
  FormControl,
  Grid,
  InputLabel,
  MenuItem,
  Select,
} from "@material-ui/core";

export default function FormDialog({
  alimentsList,
  handleSnackbar,
  setMaj,
  maj,
}) {
  const [open, setOpen] = useState(false);
  const [newInventaire, setNewInventaire] = useState({
    id: 1,
    libelle: "",
    date: "",
    entre: 0,
    commentaire: "",
  });

  const handleConfirm = () => {
    if (newInventaire.date.length !== 10) {
      handleSnackbar("danger", "Formato de fecha incorrecto");
    } else {
      var url = "/api/entre";
      const requestOptions = {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          ...newInventaire,
          entre: parseInt(newInventaire.entre),
        }),
      };
      authFetch(url, requestOptions)
        .then((response) => {
          if (response.ok) {
            return response.json();
          } else {
            throw new Error(response.statusText);
          }
        })
        .then((response) => {
          handleSnackbar("success", response.message);
          setMaj(~maj);
        })
        .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
      setNewInventaire({
        id: 1,
        libelle: "",
        entre: 0,
        commentaire: "",
        date: "",
      });
    }
  };
  return (
    <div>
      <Button
        id="ButtonDialog"
        variant="contained"
        color="primary"
        onClick={() => setOpen(true)}
      >
        Compra de alimento
      </Button>
      <Dialog
        open={open}
        onClose={() => {
          setOpen(false);
        }}
        aria-labelledby="form-dialog-title"
      >
        <DialogTitle id="form-dialog-title">Compra de alimento</DialogTitle>
        <DialogContent>
          <Grid container spacing={1} justify="center" alignItems="center">
            <Grid item xs={4}>
              <FormControl style={{ minWidth: "150px" }}>
                <InputLabel id="Alimento-label">Alimento</InputLabel>
                <Select
                  labelId="Alimento-label"
                  id="Alimento-select"
                  value={newInventaire.id}
                  onChange={(e) =>
                    setNewInventaire({ ...newInventaire, id: e.target.value })
                  }
                >
                  {alimentsList.map((a, key) => (
                    <MenuItem key={key} value={a[0]}>
                      {a[1]}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>{" "}
            </Grid>
            <Grid item xs={4}>
              <TextField
                label={"Fecha"}
                type="text"
                value={newInventaire.date}
                onChange={(e) => {
                  setNewInventaire({
                    ...newInventaire,
                    date: e.target.value,
                  });
                }}
              />{" "}
            </Grid>
            <Grid item xs={4}>
              <TextField
                label={"Cantidad"}
                type="text"
                value={newInventaire.entre}
                onChange={(e) => {
                  setNewInventaire({
                    ...newInventaire,
                    entre: e.target.value,
                  });
                }}
              />{" "}
            </Grid>
            <Grid item xs={4}>
              <TextField
                label={"Comentario"}
                type="text"
                value={newInventaire.commentaire}
                onChange={(e) => {
                  setNewInventaire({
                    ...newInventaire,
                    commentaire: e.target.value,
                  });
                }}
              />
            </Grid>
          </Grid>
        </DialogContent>
        <DialogActions>
          <Button
            onClick={() => {
              setOpen(false);
            }}
            color="primary"
          >
            Cerrar
          </Button>
          <Button onClick={handleConfirm} color="primary">
            Confirmar
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
