import React, { useState, useEffect } from "react";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import { authFetch } from "auth";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import "assets/css/App.css";
import { Grid, Typography } from "@material-ui/core";
import Snackbar from "components/Snackbar/Snackbar.js";
import { logout } from "auth";

export default function Copeyito({ ...rest }) {
  const [openUsername, setOpenUsername] = useState(false);
  const [openEmail, setOpenEmail] = useState(false);
  const [openPassword, setOpenPassword] = useState(false);
  const [openUserList, setOpenUserList] = useState(false);
  const [openAddUser, setOpenAddUser] = useState(false);
  const [user, setUser] = useState({
    name: "Default",
    email: "defaul@gmail.com",
    roles: "legacy",
    password: "",
    confirmPassword: "",
  });
  const [tc, setTC] = useState(false);
  const [message, setMessage] = useState("");
  const [color, setColor] = useState("primary");
  const [userList, setUserList] = useState([]);
  const [newUser, setNewUser] = useState({
    name: "",
    email: "",
    password: "",
    role: "",
  });
  useEffect(() => {
    authFetch("/api/user")
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        setUser({
          name: response.name,
          email: response.email,
          roles: response.role,
          password: "",
          confirmPassword: "",
        });
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
  }, [setUser]);

  useEffect(() => {
    user.roles === "admin" || user.roles === "boss"
      ? authFetch("/api/user/list")
          .then((response) => {
            if (response.ok) {
              return response.json();
            } else {
              throw new Error(response.statusText);
            }
          })
          .then((response) => {
            setUserList(response.list);
          })
          .catch((e) => handleSnackbar("danger", e.name + ": " + e.message))
      : setUserList([]);
  }, [user]);

  const handleSnackbar = (cl, ms) => {
    setColor(cl);
    setMessage(ms);
    setTC(true);
    setTimeout(function () {
      setTC(false);
      setColor("");
      setMessage("");
    }, 4000);
  };

  const handleSuppression = (username) => {
    var url = "/api/user/list";
    setOpenUserList(false);
    const requestOptions = {
      method: "PUT",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ username: username }),
    };
    authFetch(url, requestOptions)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        handleSnackbar("success", response.message);
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
  };

  const handleSubmitUser = () => {
    var url = "/api/user";
    setOpenEmail(false);
    setOpenUsername(false);
    const requestOptions = {
      method: "PUT",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        username: user.name,
        email: user.email,
      }),
    };
    authFetch(url, requestOptions)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        handleSnackbar("success", response.message);
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
  };

  const handlePasswordChange = () => {
    var url = "/api/user";
    setOpenPassword(false);
    const requestOptions = {
      method: "PUT",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        password: user.password,
      }),
    };
    authFetch(url, requestOptions)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        handleSnackbar("success", response.message);
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
  };

  const handleAddUser = () => {
    var url = "/api/user";
    setOpenAddUser(false);
    const requestOptions = {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        name: newUser.name,
        email: newUser.email,
        password: newUser.password,
        role: newUser.role,
      }),
    };
    authFetch(url, requestOptions)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        handleSnackbar("success", response.message);
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
  };

  return (
    <div>
      <Snackbar
        place="tc"
        color={color}
        message={message}
        openUsername={tc}
        closeNotification={() => setTC(false)}
        close
      />
      <div id="login">
        <Grid container spacing={1}>
          <Grid item xs={12} align="center">
            <Typography component="h2" variant="h2">
              Profile
            </Typography>
          </Grid>
          <Grid item xs={12} align="center">
            <Typography component="h6" variant="h6">
              Hi {user.name} 🙂!
            </Typography>
          </Grid>
          {/* Username */}
          <Grid item xs={12} align="center">
            <Button
              id="ButtonDialog"
              variant="contained"
              color="secondary"
              onClick={() => setOpenUsername(true)}
            >
              Change username
            </Button>
            <Dialog
              open={openUsername}
              onClose={() => setOpenUsername(false)}
              aria-labelledby="form-dialog-title"
            >
              <DialogTitle id="form-dialog-title">
                Change your username
              </DialogTitle>
              <DialogContent>
                <Grid container spacing={1}>
                  <Grid item>
                    <TextField
                      required
                      id="new_username"
                      label="New username"
                      type="text"
                      variant="filled"
                      onChange={(e) => {
                        setUser({ ...user, name: e.target.value });
                      }}
                    />
                  </Grid>
                </Grid>
              </DialogContent>
              <DialogActions>
                <Button onClick={() => setOpenUsername(false)} color="primary">
                  Annuler
                </Button>
                <Button onClick={handleSubmitUser} color="primary">
                  Change
                </Button>
              </DialogActions>
            </Dialog>
          </Grid>
          {/* Email */}
          <Grid item xs={12} align="center">
            <Button
              id="ButtonDialog"
              variant="contained"
              color="secondary"
              onClick={() => setOpenEmail(true)}
            >
              Change email
            </Button>
            <Dialog
              open={openEmail}
              onClose={() => setOpenEmail(false)}
              aria-labelledby="form-dialog-title"
            >
              <DialogTitle id="form-dialog-title">
                Change your email
              </DialogTitle>
              <DialogContent>
                <Grid container spacing={1}>
                  <Grid item>
                    <TextField
                      required
                      id="new_email"
                      label="New email"
                      type="text"
                      defaultValue={user.email}
                      variant="filled"
                      onChange={(e) =>
                        setUser({ ...user, email: e.target.value })
                      }
                    />
                  </Grid>
                </Grid>
              </DialogContent>
              <DialogActions>
                <Button onClick={() => setOpenEmail(false)} color="primary">
                  Annuler
                </Button>
                <Button onClick={handleSubmitUser} color="primary">
                  Change
                </Button>
              </DialogActions>
            </Dialog>
          </Grid>
          {/* Password */}
          <Grid item xs={12} align="center">
            <Button
              id="ButtonDialog"
              variant="contained"
              color="secondary"
              onClick={() => setOpenPassword(true)}
            >
              Change password
            </Button>
            <Dialog
              open={openPassword}
              onClose={() => setOpenPassword(false)}
              aria-labelledby="form-dialog-title"
            >
              <DialogTitle id="form-dialog-title">
                Change your password
              </DialogTitle>
              <DialogContent>
                <Grid container spacing={1}>
                  <Grid item>
                    <TextField
                      required
                      id="new_password"
                      label="New password"
                      type="password"
                      variant="filled"
                      onChange={(e) =>
                        setUser({ ...user, password: e.target.value })
                      }
                    />
                  </Grid>
                  <Grid item>
                    <TextField
                      required
                      error={
                        user.password !== user.confirmPassword &&
                        user.confirmPassword !== ""
                          ? true
                          : false
                      }
                      id="new_password_confirm"
                      label="Confirm new password"
                      type="password"
                      variant="filled"
                      onChange={(e) =>
                        setUser({ ...user, confirmPassword: e.target.value })
                      }
                    />
                  </Grid>
                </Grid>
              </DialogContent>
              <DialogActions>
                <Button onClick={() => setOpenPassword(false)} color="primary">
                  Annuler
                </Button>
                <Button onClick={handlePasswordChange} color="primary">
                  Change
                </Button>
              </DialogActions>
            </Dialog>
          </Grid>
          {/* User list */}
          {user.roles === "admin" || user.roles === "boss" ? (
            <>
              <Grid item xs={12} align="center">
                <Button
                  id="ButtonDialog"
                  variant="contained"
                  color="secondary"
                  onClick={() => setOpenUserList(true)}
                >
                  Manage Users
                </Button>
                <Dialog
                  open={openUserList}
                  onClose={() => setOpenUserList(false)}
                  aria-labelledby="form-dialog-title"
                >
                  <DialogTitle id="form-dialog-title">
                    Manage user list
                  </DialogTitle>
                  <DialogContent>
                    <Grid container spacing={1}>
                      {userList.map((user) => {
                        return (
                          <Grid item xs={12} align="center">
                            <Typography component="p">
                              Email : {user[0]}, Username : {user[1]}, Role :{" "}
                              {user[2]}, Active : {user[3] === 1 ? "yes" : "no"}
                            </Typography>
                            <Button
                              onClick={() => handleSuppression(user[1])}
                              color="primary"
                            >
                              X
                            </Button>
                          </Grid>
                        );
                      })}
                    </Grid>
                  </DialogContent>
                  <DialogActions>
                    <Button
                      onClick={() => setOpenUserList(false)}
                      color="primary"
                    >
                      Close
                    </Button>
                  </DialogActions>
                </Dialog>
              </Grid>

              <Grid item xs={12} align="center">
                <Button
                  id="ButtonDialog"
                  variant="contained"
                  color="secondary"
                  onClick={() => setOpenAddUser(true)}
                >
                  Add User
                </Button>
                <Dialog
                  open={openAddUser}
                  onClose={() => setOpenAddUser(false)}
                  aria-labelledby="form-dialog-title"
                >
                  <DialogTitle id="form-dialog-title">Add User</DialogTitle>
                  <DialogContent>
                    <Grid container spacing={1} align="center">
                      <Grid item xs={12} align="center">
                        <TextField
                          required
                          id="new_name"
                          label="Name"
                          type="text"
                          variant="filled"
                          onChange={(e) =>
                            setNewUser({ ...newUser, name: e.target.value })
                          }
                        />
                      </Grid>
                      <Grid item xs={12} align="center">
                        <TextField
                          required
                          id="new_email"
                          label="Email"
                          type="text"
                          variant="filled"
                          onChange={(e) =>
                            setNewUser({ ...newUser, email: e.target.value })
                          }
                        />
                      </Grid>
                      <Grid item xs={12} align="center">
                        <TextField
                          required
                          id="new_password"
                          label="Password"
                          type="text"
                          variant="filled"
                          onChange={(e) =>
                            setNewUser({ ...newUser, password: e.target.value })
                          }
                        />
                      </Grid>
                      <Grid item xs={12} align="center">
                        <TextField
                          required
                          id="new_role"
                          label="Role"
                          type="text"
                          variant="filled"
                          onChange={(e) =>
                            setNewUser({ ...newUser, role: e.target.value })
                          }
                        />
                      </Grid>
                    </Grid>
                  </DialogContent>
                  <DialogActions>
                    <Button
                      onClick={() => setOpenAddUser(false)}
                      color="primary"
                    >
                      Close
                    </Button>
                    <Button onClick={handleAddUser} color="primary">
                      Add
                    </Button>
                  </DialogActions>
                </Dialog>
              </Grid>
            </>
          ) : null}
          <Grid container spacing={1} item xs={12} justify="center">
            <Grid item>
              <Button
                id="buttonBack"
                variant="contained"
                color="primary"
                href="/"
              >
                Back
              </Button>
            </Grid>
            <Grid item>
              <Button
                id="buttonLogout"
                variant="contained"
                color="secondary"
                onClick={logout}
              >
                Logout
              </Button>
            </Grid>
          </Grid>
        </Grid>
      </div>
    </div>
  );
}
