import React, { useState, useEffect } from "react";
import { authFetch } from "auth";
import { makeStyles } from "@material-ui/core/styles";
import Snackbar from "components/Snackbar/Snackbar.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import "assets/css/App.css";
import {
  FormControl,
  FormHelperText,
  InputLabel,
  Select,
  MenuItem,
  Grid,
} from "@material-ui/core";
// icons
import { ArrowUpward, ArrowDownward, ArrowForward } from "@material-ui/icons";
// styles
import styles from "assets/jss/material-dashboard-react/views/dashboardStyle.js";
import DateChart from "components/DateChart";

const useStyles = makeStyles(styles);

export default function AnalysesEau({ bassinDefault }) {
  const [bassinsList, setBassinsList] = useState(["B03"]);
  const [bassin, setBassin] = useState(
    bassinDefault !== undefined ? bassinDefault : "B03"
  );
  const [tc, setTC] = useState(false);
  const [color, setColor] = useState("primary");
  const [message, setMessage] = useState("");
  const [temps, setTemps] = useState(["102"]);
  const [O2, setO2] = useState([]);
  const [ph, setPh] = useState(["102"]);
  const [SE, setSE] = useState(["102"]);
  const [majBassin, setMajBassin] = useState(false);

  let dateOneYear = new Date();
  dateOneYear.setMonth(dateOneYear.getMonth() - 12);

  useEffect(() => {
    var url = "/api/bassins";
    authFetch(url)
      .then((response) => {
        if (response.status === 401) {
          handleSnackbar("danger", "Sorry you aren't authorized!");
          return null;
        } else if (response.status === 500) {
          handleSnackbar("warning", "Bad request");
          return null;
        }
        return response.json();
      })
      .then((response) => {
        setBassinsList(response.bassins);
        setMajBassin(true);
      });
  }, []);

  useEffect(() => {
    if (majBassin) {
      var url =
        "/api/analyseEauGraph/" +
        bassin +
        "/" +
        dateOneYear.toISOString().substring(0, 10);
      authFetch(url)
        .then((response) => {
          if (response.status === 401) {
            handleSnackbar("danger", "Sorry you aren't authorized!");
            return null;
          }
          return response.json();
        })
        .then((response) => {
          setTemps(response.temps);
          setO2(response.oxygene);
          setPh(response.ph);
          setSE(response.se);
        });
    }
  }, [majBassin, bassin]);

  const handleSnackbar = (cl, ms) => {
    setColor(cl);
    setMessage(ms);
    setTC(true);
    setTimeout(function () {
      setTC(false);
      setColor("");
      setMessage("");
    }, 4000);
  };

  const handleBassinChange = (e) => {
    setBassin(e.target.value);
  };

  return (
    <div id="#copeyito" style={{ marginTop: "1em" }}>
      <Grid container spacing={1}>
        <Snackbar
          place="tc"
          color={color}
          message={message}
          open={tc}
          closeNotification={() => setTC(false)}
          close
        />
        <Grid item xs={12} spacing={1} align="center" container>
          <Grid item xs={6} align="right">
            <FormControl>
              <InputLabel id="bassin-label">Estanque</InputLabel>
              <Select
                labelId="bassin-label"
                id="bassin-select"
                value={bassin}
                onChange={handleBassinChange}
              >
                {bassinsList !== ""
                  ? bassinsList.map((Bassin, key) => (
                      <MenuItem key={key} value={Bassin}>
                        {Bassin}
                      </MenuItem>
                    ))
                  : null}
              </Select>
              <FormHelperText>Seleccione un estanque</FormHelperText>
            </FormControl>
          </Grid>
        </Grid>
        <Grid item xs={6} align="center">
          <Card chart>
            <CardHeader color="success">
              {temps && temps.length !== 0 ? (
                <DateChart
                  title={"Temperature"}
                  data={[
                    {
                      name: "temps",
                      data: temps,
                    },
                  ]}
                  dateOption={{
                    xStartDate: new Date(
                      dateOneYear.toISOString().substring(0, 10)
                    ).getTime(),
                    colors: ["#fff"],
                    theme: "dark",
                  }}
                />
              ) : null}
            </CardHeader>
            <CardBody>
              {/* <h4 className={classes.cardTitle}>Temperatura del agua</h4> */}
              {/* <p className={classes.cardCategory}> */}
              {/* {TextEvolution(temps, "Temperature")} */}
              {/* </p> */}
            </CardBody>
          </Card>
        </Grid>
        <Grid item xs={6} align="center">
          <Card chart>
            <CardHeader color="warning">
              {O2 && O2.length !== 0 ? (
                <DateChart
                  title={"O2"}
                  data={[
                    {
                      name: "O2",
                      data: O2,
                    },
                  ]}
                  dateOption={{
                    xStartDate: new Date(
                      dateOneYear.toISOString().substring(0, 10)
                    ).getTime(),
                    colors: ["#fff"],
                    theme: "dark",
                  }}
                />
              ) : null}
            </CardHeader>
            <CardBody>
              {/* <h4 className={classes.cardTitle}>Oxígeno</h4> */}
              {/* <p className={classes.cardCategory}>{TextEvolution(O2, "O2")}</p> */}
            </CardBody>
          </Card>
        </Grid>
        <Grid item xs={6} align="center">
          <Card chart>
            <CardHeader color="info">
              {ph && ph.length !== 0 ? (
                <DateChart
                  title={"PH"}
                  data={[
                    {
                      name: "PH",
                      data: ph,
                    },
                  ]}
                  dateOption={{
                    xStartDate: new Date(
                      dateOneYear.toISOString().substring(0, 10)
                    ).getTime(),
                    colors: ["#fff"],
                    theme: "dark",
                  }}
                />
              ) : null}
            </CardHeader>
            <CardBody>
              {/* <h4 className={classes.cardTitle}>PH del agua</h4> */}
              {/* <p className={classes.cardCategory}>{TextEvolution(ph, "PH")}</p> */}
            </CardBody>
          </Card>
        </Grid>
        <Grid item xs={6} align="center">
          <Card chart>
            <CardHeader color="danger">
              {SE && SE.length !== 0 ? (
                <DateChart
                  title={"SE"}
                  data={[
                    {
                      name: "SE",
                      data: SE,
                    },
                  ]}
                  dateOption={{
                    xStartDate: new Date(
                      dateOneYear.toISOString().substring(0, 10)
                    ).getTime(),
                    colors: ["#fff"],
                    theme: "dark",
                  }}
                />
              ) : null}
            </CardHeader>
            <CardBody>
              {/* <h4 className={classes.cardTitle}>SE del agua</h4> */}
              {/* <p className={classes.cardCategory}>{TextEvolution(SE, "SE")}</p> */}
            </CardBody>
          </Card>
        </Grid>
      </Grid>
    </div>
  );
}

const computeEvolution = (list) => {
  return (
    ((parseFloat(list[list.length - 1]) - parseFloat(list[list.length - 2])) /
      parseFloat(list[list.length - 2])) *
    100
  );
};

const TextEvolution = (list, name) => {
  const classes = useStyles();
  const evol = computeEvolution(list);
  if (isNaN(evol)) {
    return null;
  }
  if (evol < 0) {
    return (
      <span className={classes.dangerText}>
        <ArrowDownward className={classes.upArrowCardCategory} />
        {evol}% disminución en el día de hoy.
      </span>
    );
  } else if (evol === 0) {
    return (
      <span className={classes.successText}>
        <ArrowForward className={classes.upArrowCardCategory} />
        0% evolución.
      </span>
    );
  } else {
    return (
      <span className={classes.successText}>
        <ArrowUpward className={classes.upArrowCardCategory} />
        {evol}% aumento en el día de hoy .
      </span>
    );
  }
};
