import React, { useState, useEffect } from "react";

import { authFetch } from "auth";
import {
  Grid,
  Button,
  TextField,
  CardHeader,
  CircularProgress,
} from "@material-ui/core";
import Card from "components/Card/Card.js";
import CardBody from "components/Card/CardBody.js";
import Snackbar from "components/Snackbar/Snackbar.js";

import { makeStyles } from "@material-ui/core/styles";
import styles from "assets/jss/material-dashboard-react/components/cardHeaderStyle.js";

const useStyles = makeStyles(styles);

export default function AddAnalyseEau() {
  const [tc, setTC] = useState(false);
  const [message, setMessage] = useState("");
  const [color, setColor] = useState("primary");
  const [loading, setLoading] = useState(true);

  const [cyclesList, setCyclesList] = useState(undefined);
  const [cycle, setCycle] = useState(undefined);
  const [date, setDate] = useState(new Date().toISOString().substring(0, 10));
  const [fetchDate, setFetchDate] = useState("");
  const [o2ToSend0, setO2ToSend0] = useState({ O2: "", heure: 17 });
  const [o2ToSend1, setO2ToSend1] = useState({ O2: "", heure: 22 });
  const [o2ToSend2, setO2ToSend2] = useState({ O2: "", heure: 0 });
  const [o2ToSend3, setO2ToSend3] = useState({ O2: "", heure: 4 });
  const [maj, setMaj] = useState(false);

  const classes = useStyles();

  useEffect(() => {
    setLoading(true);
  }, [date]);

  useEffect(() => {
    var url = "/api/analyseEau/" + date;
    authFetch(url)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        setCyclesList(
          response.cycles.sort((a, b) => (a.Bassin > b.Bassin ? 1 : -1))
        );
        setFetchDate(response.date);
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
  }, [date, maj]);

  useEffect(() => {
    if (cycle !== undefined) {
      setO2ToSend0({ O2: "", heure: 17 });
      setO2ToSend1({ O2: "", heure: 22 });
      setO2ToSend2({ O2: "", heure: 0 });
      setO2ToSend3({ O2: "", heure: 4 });
      for (let i = 0; i < cycle.Ox_list.length; i++) {
        const h = cycle.Ox_list[i][4];
        const O2_temp = cycle.Ox_list[i][5];
        if (h === 17) {
          setO2ToSend0({ O2: O2_temp, heure: 17 });
        } else if (h === 22) {
          setO2ToSend1({ O2: O2_temp, heure: 22 });
        } else if (h === 0) {
          setO2ToSend2({ O2: O2_temp, heure: 0 });
        } else if (h === 4) {
          setO2ToSend3({ O2: O2_temp, heure: 4 });
        }
      }
    }
    if (
      cycle !== undefined &&
      (cycle.PH === undefined ||
        cycle.Temp === undefined ||
        cycle.SE === undefined)
    ) {
      setCycle({ ...cycle, PH: "", Temp: "", SE: "" });
    }
  }, [cycle]);

  useEffect(() => {
    if (~loading) {
      if (fetchDate !== date) {
        setDate(date);
        setLoading(true);
      } else {
        setLoading(false);
      }
    }
  }, [loading, date, fetchDate]);

  const handleSnackbar = (cl, ms) => {
    setColor(cl);
    setMessage(ms);
    setTC(true);
    setTimeout(function () {
      setTC(false);
      setColor("");
      setMessage("");
    }, 4000);
  };

  const handleConfirm = () => {
    console.log(o2ToSend2);
    if (
      parseInt(o2ToSend0.O2) > 20 ||
      parseInt(o2ToSend1.O2) > 20 ||
      parseInt(o2ToSend2.O2) > 20 ||
      parseInt(o2ToSend3.O2) > 20
    ) {
      handleSnackbar("warning", "Valor de oxígeno demasiado alto (mas de 20)");
    } else {
      let temp = cyclesList;
      temp[cycle.id] = {
        ...cycle,
        Ox_list: [],
        analyse: true,
        maj: true,
      };
      setCyclesList(temp);
      var url = "/api/analyseEau";
      var requestOptions = {
        method: "PUT",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ ...cycle, bassin: cycle.Bassin, date: date }),
      };
      authFetch(url, requestOptions)
        .then((response) => {
          if (response.ok) {
            return response.json();
          } else {
            throw new Error(response.statusText);
          }
        })
        .then((response) => {
          handleSnackbar("success", response.message);
        })
        .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
      url = "/api/analyseOx";
      requestOptions = {
        method: "PUT",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          data: [o2ToSend0, o2ToSend1, o2ToSend2, o2ToSend3],
          date: date,
          bassin: cycle.Bassin,
        }),
      };
      authFetch(url, requestOptions)
        .then((response) => {
          if (response.ok) {
            return response.json();
          } else {
            throw new Error(response.statusText);
          }
        })
        .then((response) => {
          handleSnackbar("success", response.message);
          setMaj(~maj);
        })
        .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
    }
  };

  const changeDate = (d) => {
    var result = new Date(date + "T12:00:00");
    result.setDate(result.getDate() + d);
    setDate(result.toISOString().substring(0, 10));
    setCycle(undefined);
  };

  return (
    <div>
      <Snackbar
        place="tc"
        color={color}
        message={message}
        open={tc}
        closeNotification={() => setTC(false)}
        close
      />
      <Grid container spacing={2}>
        <Grid style={{ padding: "0" }} item xs={12} container justify="center">
          <Button
            onClick={() => changeDate(-1)}
            variant="contained"
            color="primary"
          >
            {"<<"}
          </Button>
          <Button
            style={{ marginLeft: "5px" }}
            variant="contained"
            color="primary"
          >
            {date.split("-").reverse().join("-")}
          </Button>
          <Button
            style={{ marginLeft: "5px" }}
            onClick={() => changeDate(1)}
            variant="contained"
            color="primary"
          >
            {">>"}
          </Button>
        </Grid>
        <Grid
          container
          spacing={1}
          item
          xs={4}
          style={{ overflow: "scroll", height: "100vh" }}
        >
          {cyclesList &&
            cyclesList.map((c, key) => (
              <Grid item xs={12} key={key}>
                <Button
                  style={{
                    height: "80px",
                    color: "white",
                    backgroundColor: c.analyse ? "#4caf50" : "#3f51b5",
                  }}
                  id={key}
                  variant="contained"
                  fullWidth
                  onClick={(e) => {
                    e.target.id === ""
                      ? setCycle({
                          ...cyclesList[e.target.offsetParent.id],
                          id: e.target.offsetParent.id,
                        })
                      : setCycle({
                          ...cyclesList[e.target.id],
                          id: e.target.id,
                        });
                  }}
                >
                  {c.Bassin}
                </Button>
              </Grid>
            ))}
        </Grid>
        <Grid item xs={8}>
          {cycle ? (
            <Card>
              <CardHeader
                className={classes.cardHeader}
                title={cycle.Bassin}
                titleTypographyProps={{ variant: "h2", align: "center" }}
                style={{
                  backgroundColor: "#009688",
                  padding: "20px",
                  color: "white",
                  fontSize: "2rem",
                }}
              ></CardHeader>
              <CardBody>
                <Grid
                  container
                  spacing={2}
                  justify="center"
                  alignItems="center"
                >
                  <Grid item xs={10}>
                    <Grid
                      container
                      spacing={2}
                      justify="center"
                      alignItems="center"
                    >
                      <Grid item xs={2} style={{ textAlign: "center" }}>
                        <TextField
                          variant="outlined"
                          label="PH"
                          InputLabelProps={{ shrink: true }}
                          value={cycle.PH}
                          autoComplete="off"
                          onChange={(e) =>
                            setCycle({ ...cycle, PH: e.target.value })
                          }
                        ></TextField>
                      </Grid>
                      <Grid item xs={2} style={{ textAlign: "center" }}>
                        <TextField
                          variant="outlined"
                          label="Temp"
                          InputLabelProps={{ shrink: true }}
                          value={cycle.Temp}
                          autoComplete="off"
                          onChange={(e) =>
                            setCycle({ ...cycle, Temp: e.target.value })
                          }
                        ></TextField>
                      </Grid>
                      <Grid item xs={2} style={{ textAlign: "center" }}>
                        <TextField
                          variant="outlined"
                          label="SE"
                          InputLabelProps={{ shrink: true }}
                          value={cycle.SE}
                          autoComplete="off"
                          onChange={(e) =>
                            setCycle({ ...cycle, SE: e.target.value })
                          }
                        ></TextField>
                      </Grid>
                    </Grid>
                    <Grid
                      container
                      spacing={2}
                      justify="center"
                      alignItems="center"
                    >
                      <Grid item xs={2} style={{ textAlign: "center" }}>
                        <TextField
                          id="text-2"
                          variant="outlined"
                          label="12 a.m"
                          value={o2ToSend2.O2}
                          autoComplete="off"
                          onChange={(e) => {
                            setO2ToSend2({ ...o2ToSend2, O2: e.target.value });
                          }}
                        ></TextField>
                      </Grid>
                      <Grid item xs={2} style={{ textAlign: "center" }}>
                        <TextField
                          id="text-3"
                          variant="outlined"
                          label="4 a.m"
                          value={o2ToSend3.O2}
                          autoComplete="off"
                          onChange={(e) => {
                            setO2ToSend3({ ...o2ToSend3, O2: e.target.value });
                          }}
                        ></TextField>
                      </Grid>
                      <Grid item xs={2} style={{ textAlign: "center" }}>
                        <TextField
                          id="text-0"
                          variant="outlined"
                          label="5 p.m"
                          value={o2ToSend0.O2}
                          autoComplete="off"
                          onChange={(e) => {
                            setO2ToSend0({ ...o2ToSend0, O2: e.target.value });
                          }}
                        ></TextField>
                      </Grid>
                      <Grid item xs={2} style={{ textAlign: "center" }}>
                        <TextField
                          id="text-1"
                          variant="outlined"
                          label="10 p.m"
                          value={o2ToSend1.O2}
                          autoComplete="off"
                          onChange={(e) => {
                            setO2ToSend1({ ...o2ToSend1, O2: e.target.value });
                          }}
                        ></TextField>
                      </Grid>
                    </Grid>
                  </Grid>
                  <Grid container item xs={2} alignItems="center">
                    <Button
                      onClick={handleConfirm}
                      variant="contained"
                      color="primary"
                    >
                      Confirmar
                    </Button>
                  </Grid>
                </Grid>
              </CardBody>
            </Card>
          ) : loading ? (
            <Grid
              style={{ height: "80%" }}
              container
              spacing={2}
              alignItems="center"
              justify="center"
            >
              <Grid item>
                <CircularProgress
                  style={{
                    display: "flex",
                    height: "150px",
                    width: "150px",
                  }}
                />
              </Grid>
            </Grid>
          ) : null}
        </Grid>
      </Grid>
    </div>
  );
}
