import React, { useEffect, useState } from "react";
import { Switch, Route, Redirect } from "react-router-dom";
import "../assets/css/App.css";
// creates a beautiful scrollbar
import PerfectScrollbar from "perfect-scrollbar";
import "perfect-scrollbar/css/perfect-scrollbar.css";
import Navbar from "components/Navbars/Navbar.js";
import Sidebar from "components/Sidebar.js";
import { makeStyles } from "@material-ui/core/styles";
import routes from "routes.js";
import AncienCycle from "../views/AncienCycle";

import { useAuth, authFetch } from "../auth";

import image from "assets/img/sidebar-2.jpg";
import logo from "assets/img/logo.png";

import styles from "assets/jss/material-dashboard-react/layouts/adminStyle.js";
const useStyles = makeStyles(styles);

let ps;

const PrivateRoute = ({ component: Component, roles, ...rest }) => {
  const [logged] = useAuth();
  const [role, setRole] = useState(undefined);

  useEffect(() => {
    var url = "/api/user";
    authFetch(url)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        setRole(response.role);
      });
  }, []);

  return roles && roles.includes(role) ? (
    <Route
      {...rest}
      render={(props) =>
        logged ? <Component {...props} /> : <Redirect to="/login" />
      }
    />
  ) : null;
};

const switchRoutes = (
  <Switch>
    {routes.map((prop, key) => {
      if (prop.layout === "/copeyito") {
        return (
          <PrivateRoute
            path={prop.layout + prop.path}
            component={prop.component}
            key={key}
            roles={prop.roles}
          />
        );
      }
      return null;
    })}
    <Route
      path="/copeyito/ancienCycle/:bassin/:dateDebut/:dateFin"
      component={AncienCycle}
    />
    <Redirect from="/copeyito" to="/copeyito/dashboard" />
  </Switch>
);

export default function Copeyito({ ...rest }) {
  const classes = useStyles();
  const [mobileOpen, setMobileOpen] = React.useState(false);
  // ref to help us initialize PerfectScrollbar on windows devices
  const mainPanel = React.createRef();
  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };
  const resizeFunction = () => {
    if (window.innerWidth >= 960) {
      setMobileOpen(false);
    }
  };
  // initialize and destroy the PerfectScrollbar plugin
  useEffect(() => {
    if (navigator.platform.indexOf("Win") > -1) {
      ps = new PerfectScrollbar(mainPanel.current, {
        suppressScrollX: true,
        suppressScrollY: false,
      });
      document.body.style.overflow = "hidden";
    }
    window.addEventListener("resize", resizeFunction);
    // Specify how to clean up after this effect:
    return function cleanup() {
      if (navigator.platform.indexOf("Win") > -1) {
        ps.destroy();
      }
      window.removeEventListener("resize", resizeFunction);
    };
  }, [mainPanel]);

  return (
    <div className={classes.wrapper}>
      <Sidebar
        routes={routes}
        logoText={"Copeyito"}
        logo={logo}
        image={image}
        handleDrawerToggle={handleDrawerToggle}
        open={mobileOpen}
        color={"blue"}
        {...rest}
      />
      <div className={classes.mainPanel} ref={mainPanel}>
        <Navbar
          routes={routes}
          handleDrawerToggle={handleDrawerToggle}
          {...rest}
        />
        <div
          style={{
            padding: "5px",
            marginTop: "50px",
          }}
          className={classes.content}
        >
          <div style={{ padding: "0" }} className={classes.container}>
            {switchRoutes}
          </div>
        </div>
      </div>
    </div>
  );
}
