import { useState, useEffect } from "react";
import {
  Button,
  CardHeader,
  Card,
  CardContent,
  Grid,
  TextField,
  Typography,
} from "@material-ui/core";
import CustomTableEdit from "components/TableEdit";
import CardMat from "components/Card/Card.js";
import CardHeaderMat from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import Snackbar from "components/Snackbar/Snackbar.js";
import { authFetch } from "auth";

export default function Venta() {
  const [venteCrevette, setVenteCrevette] = useState({
    date: "",
    u5: 0,
    u6: 0,
    u8: 0,
    u10: 0,
    u12: 0,
    u15: 0,
    u16_u20: 0,
    u21_u25: 0,
    decortique: 0,
  });
  const [tc, setTC] = useState(false);
  const [message, setMessage] = useState("");
  const [color, setColor] = useState("primary");
  const [tableHeaders] = useState([
    "fecha",
    "u5",
    "u6",
    "u8",
    "u10",
    "u12",
    "u15",
    "u16/20",
    "u21/25",
    "pelado",
    "total venta",
    "total pesca",
    "% perdida",
  ]);
  const [tableData, setTableData] = useState("");
  const [maj, setMaj] = useState(true);

  useEffect(() => {
    var url = "/api/ventes/crevettes";
    authFetch(url)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        setTableData(response.ventes);
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
  }, [maj]);

  const handleConfirm = () => {
    var url = "/api/ventes/crevettes";
    const requestOptions = {
      method: "PUT",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        ...venteCrevette,
        date:
          venteCrevette.date.substring(6, 10) +
          "-" +
          venteCrevette.date.substring(3, 5) +
          "-" +
          venteCrevette.date.substring(0, 2),
      }),
    };
    authFetch(url, requestOptions)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        if (response.message === "Fecha ya introducida") {
          handleSnackbar("warning", response.message);
        } else {
          handleSnackbar("success", response.message);
          setMaj(~maj);
        }
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
  };

  const handleSnackbar = (cl, ms) => {
    setColor(cl);
    setMessage(ms);
    setTC(true);
    setTimeout(function () {
      setTC(false);
      setColor("");
      setMessage("");
    }, 4000);
  };

  return (
    <Grid container spacing={1} justify="center" alignItems="center">
      <Snackbar
        place="tc"
        color={color}
        message={message}
        open={tc}
        closeNotification={() => setTC(false)}
        close
      />
      <Grid item>
        <Card>
          <CardHeader
            style={{ textAlign: "center" }}
            title="Añadir una venta de camarones"
          />
          <CardContent>
            <Grid
              item
              container
              spacing={3}
              justify="center"
              alignItems="center"
            >
              <Grid item xs={2}>
                <TextField
                  required
                  label="Fecha dd/MM/yyyy"
                  type="text"
                  value={venteCrevette.date}
                  onChange={(e) =>
                    setVenteCrevette({ ...venteCrevette, date: e.target.value })
                  }
                />
              </Grid>
              <Grid item xs={1}>
                <TextField
                  required
                  type="number"
                  label="u5"
                  value={venteCrevette.u5}
                  onChange={(e) =>
                    setVenteCrevette({
                      ...venteCrevette,
                      u5: e.target.value,
                    })
                  }
                />
              </Grid>
              <Grid item xs={1}>
                <TextField
                  required
                  type="number"
                  label="u6"
                  value={venteCrevette.u6}
                  onChange={(e) =>
                    setVenteCrevette({
                      ...venteCrevette,
                      u6: e.target.value,
                    })
                  }
                />
              </Grid>
              <Grid item xs={1}>
                <TextField
                  required
                  type="number"
                  label="u8"
                  value={venteCrevette.u8}
                  onChange={(e) =>
                    setVenteCrevette({
                      ...venteCrevette,
                      u8: e.target.value,
                    })
                  }
                />
              </Grid>
              <Grid item xs={1}>
                <TextField
                  required
                  type="number"
                  label="u10"
                  value={venteCrevette.u10}
                  onChange={(e) =>
                    setVenteCrevette({
                      ...venteCrevette,
                      u10: e.target.value,
                    })
                  }
                />
              </Grid>
              <Grid item xs={1}>
                <TextField
                  required
                  type="number"
                  label="u12"
                  value={venteCrevette.u12}
                  onChange={(e) =>
                    setVenteCrevette({
                      ...venteCrevette,
                      u12: e.target.value,
                    })
                  }
                />
              </Grid>
              <Grid item xs={1}>
                <TextField
                  required
                  type="number"
                  label="u15"
                  value={venteCrevette.u15}
                  onChange={(e) =>
                    setVenteCrevette({
                      ...venteCrevette,
                      u15: e.target.value,
                    })
                  }
                />
              </Grid>
              <Grid item xs={1}>
                <TextField
                  required
                  type="number"
                  label="u16_u20"
                  value={venteCrevette.u16_u20}
                  onChange={(e) =>
                    setVenteCrevette({
                      ...venteCrevette,
                      u16_u20: e.target.value,
                    })
                  }
                />
              </Grid>
              <Grid item xs={1}>
                <TextField
                  required
                  type="number"
                  label="u21_u25"
                  value={venteCrevette.u21_u25}
                  onChange={(e) =>
                    setVenteCrevette({
                      ...venteCrevette,
                      u21_u25: e.target.value,
                    })
                  }
                />
              </Grid>
              <Grid item xs={1}>
                <TextField
                  required
                  type="number"
                  label="Décortiqué"
                  value={venteCrevette.decortique}
                  onChange={(e) =>
                    setVenteCrevette({
                      ...venteCrevette,
                      decortique: e.target.value,
                    })
                  }
                />
              </Grid>
              <Grid item>
                <Button
                  onClick={() => handleConfirm()}
                  variant="contained"
                  color="primary"
                >
                  Confirmar
                </Button>
              </Grid>
            </Grid>
          </CardContent>
        </Card>
        <Grid item>
          {tableHeaders === "" || tableData === "" ? null : (
            <CardMat style={{ marginTop: "50px" }}>
              <CardHeaderMat color="info">
                <Typography variant="h4"> Venta de camarones</Typography>
              </CardHeaderMat>
              <CardBody>
                {console.log(tableData)}
                <CustomTableEdit
                  tableHeaderColor="info"
                  tableHead={tableHeaders}
                  tableData={tableData}
                  postUrl={"/api/ventes/crevettes"}
                />
              </CardBody>
            </CardMat>
          )}
        </Grid>
      </Grid>
    </Grid>
  );
}
