import React, { useState, useEffect } from "react";
import { authFetch } from "auth/index";
import { makeStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Input from "@material-ui/core/Input";
import Paper from "@material-ui/core/Paper";
import IconButton from "@material-ui/core/IconButton";
import Snackbar from "components/Snackbar/Snackbar.js";
// Icons
import EditIcon from "@material-ui/icons/EditOutlined";
import DoneIcon from "@material-ui/icons/DoneAllTwoTone";
import RevertIcon from "@material-ui/icons/NotInterestedOutlined";
import styles from "assets/jss/material-dashboard-react/components/tableStyle.js";
import DeleteIcon from "@material-ui/icons/Delete";

const useStyles = makeStyles(styles);

const CustomTableCell = ({ row, name, onChange }) => {
  const classes = useStyles();
  const { isEditMode } = row;
  return (
    <TableCell
      style={{
        fontSize: "0.85rem",
        fontWeight: "300",
        padding: "5px",
      }}
      align="left"
      className={classes.tableCell}
    >
      {isEditMode ? (
        <Input
          value={row[name]}
          name={name}
          onChange={(e) => onChange(e, row)}
          className={classes.tableCell}
        />
      ) : (
        row[name]
      )}
    </TableCell>
  );
};

export default function CustomTableEdit(props) {
  const {
    tableHead,
    tableData,
    tableHeaderColor,
    postUrl,
    deleteUrl,
    info_for_post,
    setMaj,
    maj,
  } = props;
  const [rows, setRows] = React.useState(
    tableData.map((data) => ({ ...data, ...{ isEditMode: false } }))
  );
  const [previous, setPrevious] = React.useState({});
  const classes = useStyles();
  const [tc, setTC] = useState(false);
  const [message, setMessage] = useState("");
  const [color, setColor] = useState("primary");

  const handleSnackbar = (cl, ms) => {
    setColor(cl);
    setMessage(ms);
    setTC(true);
    setTimeout(function () {
      setTC(false);
      setColor("");
      setMessage("");
    }, 4000);
  };
  useEffect(() => {
    let setNull = (obj) => Object.keys(obj).forEach((k) => (obj[k] = null));
    let temp1 = tableData.map((data) => ({
      ...data,
      ...{ isEditMode: false },
    }));
    let temp = [...temp1, { ...temp1[0] }];
    setNull(temp[temp.length - 1]);
    temp[temp.length - 1]["id"] = temp.length;
    setRows(tableData.map((data) => ({ ...data, ...{ isEditMode: false } })));
  }, [tableData]);

  const onToggleEditMode = (id, change) => {
    setRows((state) => {
      return rows.map((row) => {
        if (row.id === id) {
          if (row.isEditMode && change) {
            delete row.isEditMode;
            const requestOptions = {
              method: "POST",
              headers: { "Content-Type": "application/json" },
              body: JSON.stringify({ info: info_for_post, ...row }),
            };
            authFetch(postUrl, requestOptions)
              .then((response) => {
                if (response.ok) {
                  return response.json();
                } else {
                  throw new Error(response.statusText);
                }
              })
              .then((response) => {
                try {
                  setMaj(~maj);
                } catch {
                  console.log("pas de maj");
                }

                handleSnackbar("success", response.message);
              })
              .catch((e) =>
                handleSnackbar("danger", e.name + ": " + e.message)
              );
            return { ...row, isEditMode: false };
          } else {
            return { ...row, isEditMode: !row.isEditMode };
          }
        }
        return row;
      });
    });
  };

  const onChange = (e, row) => {
    if (!previous[row.id]) {
      setPrevious((state) => ({ ...state, [row.id]: row }));
    }
    const value = e.target.value;
    const name = e.target.name;
    const { id } = row;
    const newRows = rows.map((row) => {
      if (row.id === id) {
        return { ...row, [name]: value };
      }
      return row;
    });
    setRows(newRows);
  };

  const onRevert = (id) => {
    const newRows = rows.map((row) => {
      if (row.id === id) {
        return previous[id] ? previous[id] : row;
      }
      return row;
    });
    setRows(newRows);
    setPrevious((state) => {
      delete state[id];
      return state;
    });
    onToggleEditMode(id, false);
  };

  const onDelete = (id) => {
    const requestOptions = {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ id: id }),
    };
    authFetch(deleteUrl, requestOptions)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        handleSnackbar("success", response.message);
        setMaj(~maj);
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
  };

  return (
    <div>
      <Snackbar
        place="tc"
        color={color}
        message={message}
        open={tc}
        closeNotification={() => setTC(false)}
        close
      />
      <Paper className={classes.root} style={{ marginTop: "0px" }}>
        <Table
          stickyHeader
          className={classes.table}
          aria-label="caption table"
        >
          <TableHead className={classes[tableHeaderColor + "TableHeader"]}>
            <TableRow
              className={classes.tableHeadRow}
              style={{ height: "40px" }}
            >
              <TableCell></TableCell>
              {tableHead.map((prop) => {
                return prop === "id" ? null : (
                  <TableCell
                    className={classes.tableCell + " " + classes.tableHeadCell}
                    key={prop.id}
                  >
                    {prop}
                  </TableCell>
                );
              })}
            </TableRow>
          </TableHead>
          <TableBody>
            {rows !== undefined
              ? rows.map((row) => (
                  <TableRow className={classes.tableRow} key={row.id}>
                    <TableCell className={classes.selectTableCell}>
                      {row.isEditMode ? (
                        <>
                          <IconButton
                            style={{ padding: "5px" }}
                            aria-label="done"
                            onClick={() => onToggleEditMode(row.id, true)}
                          >
                            <DoneIcon />
                          </IconButton>
                          <IconButton
                            style={{ padding: "5px" }}
                            aria-label="revert"
                            onClick={() => onRevert(row.id)}
                          >
                            <RevertIcon />
                          </IconButton>
                        </>
                      ) : (
                        <>
                          <IconButton
                            style={{ padding: "2px" }}
                            aria-label="delete"
                            onClick={() => onToggleEditMode(row.id)}
                          >
                            <EditIcon />
                          </IconButton>
                          <IconButton
                            style={{ padding: "2px" }}
                            aria-label="delete"
                            onClick={() => onDelete(row.id)}
                          >
                            <DeleteIcon />
                          </IconButton>
                        </>
                      )}
                    </TableCell>
                    {tableHead.map((item, key) => {
                      return item === "id" ? null : (
                        <CustomTableCell
                          key={key}
                          {...{ row, name: item, onChange }}
                        />
                      );
                    })}
                  </TableRow>
                ))
              : null}
          </TableBody>
        </Table>
      </Paper>
    </div>
  );
}
