import React, { useState, useEffect } from "react";
import { authFetch } from "auth/index";
import { makeStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Input from "@material-ui/core/Input";
import Paper from "@material-ui/core/Paper";
import IconButton from "@material-ui/core/IconButton";
import { Button } from "@material-ui/core";
// Icons
import EditIcon from "@material-ui/icons/EditOutlined";
import DoneIcon from "@material-ui/icons/DoneAllTwoTone";
import RevertIcon from "@material-ui/icons/NotInterestedOutlined";
import styles from "assets/jss/material-dashboard-react/components/tableStyle.js";
import Lots from "components/Lots";
import CloseCycleDialog from "components/Dialog/CloseCycleDialog";

const useStyles = makeStyles(styles);

const editable = ["Estanque", "Fecha lleno", "Superficie"];

const CustomTableCell = ({ row, name, onChange, i }) => {
  const classes = useStyles();
  const { isEditMode } = row;
  return (
    <TableCell
      key={"cell-custom-" + row.id + "-" + i}
      align="left"
      className={classes.tableCell}
    >
      {isEditMode && editable.includes(name) ? (
        <Input
          key={"input-custom-" + row.id + "-" + i}
          value={row[name]}
          name={name}
          onChange={(e) => onChange(e, row)}
          className={classes.tableCell}
        />
      ) : (
        row[name]
      )}
    </TableCell>
  );
};

export default function CustomTableEdit(props) {
  const {
    tableHead,
    tableData,
    tableHeaderColor,
    postUrl,
    newLine,
    handleSnackbar,
    maj,
    setMaj,
  } = props;
  const [rows, setRows] = useState(
    tableData.map((data) => ({ ...data, ...{ isEditMode: false } }))
  );
  const [previous, setPrevious] = useState({});
  const classes = useStyles();
  const [openCycle, setOpenCycle] = useState({ bool: false, cycle: 0 });

  useEffect(() => {
    let setNull = (obj) => Object.keys(obj).forEach((k) => (obj[k] = null));
    let temp1 = tableData.map((data) => ({
      ...data,
      ...{ isEditMode: false },
    }));
    let temp = [...temp1, { ...temp1[0] }];
    setNull(temp[temp.length - 1]);
    temp[temp.length - 1]["id"] = temp.length;
    newLine
      ? setRows(temp)
      : setRows(
          tableData.map((data) => ({ ...data, ...{ isEditMode: false } }))
        );
  }, [tableData, newLine]);

  const onToggleEditMode = (id, change) => {
    setRows((state) => {
      return rows.map((row) => {
        if (row.id === id) {
          if (row.isEditMode && change) {
            delete row.isEditMode;
            const data = {
              id: row["id"],
              Estanque: row["Estanque"],
              "Fecha lleno": row["Fecha lleno"],
              "Fecha vacio": row["Fecha vacio"],
              Superficie: row["Superficie"],
            };
            const requestOptions = {
              method: "POST",
              headers: { "Content-Type": "application/json" },
              body: JSON.stringify(data),
            };
            authFetch(postUrl, requestOptions)
              .then((response) => {
                return response.json();
              })
              .then((response) => {
                handleSnackbar("success", response.message);
              })
              .catch((e) => {
                handleSnackbar("warning", e);
              });
            return { ...row, isEditMode: false };
          } else {
            return { ...row, isEditMode: !row.isEditMode };
          }
        }
        return row;
      });
    });
  };

  const onChange = (e, row) => {
    if (!previous[row.id]) {
      setPrevious((state) => ({ ...state, [row.id]: row }));
    }
    const value = e.target.value;
    const name = e.target.name;
    const { id } = row;
    const newRows = rows.map((row) => {
      if (row.id === id) {
        return { ...row, [name]: value };
      }
      return row;
    });
    setRows(newRows);
  };

  const onRevert = (id) => {
    const newRows = rows.map((row) => {
      if (row.id === id) {
        return previous[id] ? previous[id] : row;
      }
      return row;
    });
    setRows(newRows);
    setPrevious((state) => {
      delete state[id];
      return state;
    });
    onToggleEditMode(id, false);
  };

  const resetCycle = () => {
    setOpenCycle({ bool: false, cycle: 0 });
    setMaj(~maj);
  };

  return (
    <Paper
      style={{
        margin: "0px",
        height: "100%",
        maxHeight: "calc(100vh - 180px)",
      }}
      className={classes.root}
    >
      {openCycle.bool ? (
        <Lots cycle_={openCycle.cycle} resetCycle={resetCycle}></Lots>
      ) : (
        <Table className={classes.table} aria-label="caption table">
          <TableHead className={classes[tableHeaderColor + "TableHeader"]}>
            <TableRow className={classes.tableHeadRow}>
              <TableCell></TableCell>
              {tableHead.map((prop, key) => {
                return prop === "id" ? null : (
                  <TableCell
                    className={classes.tableCell + " " + classes.tableHeadCell}
                    key={"cell-" + key}
                  >
                    {prop}
                  </TableCell>
                );
              })}
            </TableRow>
          </TableHead>
          <TableBody>
            {rows !== undefined
              ? rows.map((row, key) => (
                  <TableRow key={key}>
                    <TableCell key={key} className={classes.selectTableCell}>
                      {row.isEditMode ? (
                        <>
                          <IconButton
                            size="small"
                            aria-label="done"
                            onClick={() => onToggleEditMode(row.id, true)}
                          >
                            <DoneIcon />
                          </IconButton>
                          <IconButton
                            size="small"
                            aria-label="revert"
                            onClick={() => onRevert(row.id)}
                          >
                            <RevertIcon />
                          </IconButton>
                        </>
                      ) : (
                        <IconButton
                          size="small"
                          aria-label="delete"
                          onClick={() => onToggleEditMode(row.id)}
                        >
                          <EditIcon />
                        </IconButton>
                      )}
                    </TableCell>
                    {tableHead.map((item, i) => {
                      return item === "id" ? null : (
                        <CustomTableCell
                          id={i}
                          key={i}
                          {...{ row, name: item, onChange }}
                        />
                      );
                    })}
                    <TableCell style={{ padding: 0 }}>
                      <Button
                        id={row.id}
                        color="secondary"
                        variant="contained"
                        onClick={(e) => {
                          e.target.id === ""
                            ? setOpenCycle({
                                bool: true,
                                cycle: e.target.parentNode.id,
                              })
                            : setOpenCycle({ bool: true, cycle: e.target.id });
                        }}
                      >
                        +
                      </Button>
                    </TableCell>
                    <TableCell style={{ padding: 0 }}>
                      <CloseCycleDialog
                        handleSnackbar={handleSnackbar}
                        maj={maj}
                        setMaj={setMaj}
                        cycle={row.id}
                      />
                    </TableCell>
                  </TableRow>
                ))
              : null}
          </TableBody>
        </Table>
      )}
    </Paper>
  );
}
