import React, { useState } from "react";
import Button from "@material-ui/core/Button";
import { authFetch } from "auth";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import "assets/css/App.css";
import { Grid, TextField } from "@material-ui/core";

export default function FormDialog({ handleSnackbar, maj, setMaj, cycle }) {
  const [open, setOpen] = useState(false);
  const [dateFin, setDateFin] = useState("");

  const handleClose = () => {
    if (dateFin.length !== 10) {
      handleSnackbar("danger", "Formato de fecha incorrecto");
    } else {
      setOpen(false);
      var url = "/api/closeCycle";
      const requestOptions = {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          cycle: cycle,
          dateFin: dateFin,
        }),
      };
      authFetch(url, requestOptions)
        .then((response) => {
          if (response.status === 401) {
            return null;
          } else if (response.status === 500) {
            return null;
          }
          return response.json();
        })
        .then((response) => {
          response !== null
            ? handleSnackbar("success", response.message)
            : handleSnackbar("warning", "Bad request");
          setMaj(~maj);
        });
    }
  };

  return (
    <div>
      <Button
        color="primary"
        variant="contained"
        onClick={(e) => setOpen(true)}
      >
        x
      </Button>
      <Dialog
        open={open}
        onClose={() => {
          setOpen(false);
          setDateFin("");
        }}
        aria-labelledby="form-dialog-title"
      >
        <DialogTitle id="form-dialog-title"> Cerrar un ciclo</DialogTitle>
        <DialogContent>
          <Grid container spacing={1}>
            <Grid item>
              <TextField
                required
                label="Fecha fin dd/MM/yyyy"
                type="text"
                value={dateFin}
                onChange={(e) => setDateFin(e.target.value)}
              />
            </Grid>
          </Grid>
        </DialogContent>
        <DialogActions>
          <Button
            onClick={() => {
              setOpen(false);
            }}
            color="primary"
          >
            Cancelar
          </Button>
          <Button onClick={handleClose} color="primary">
            Cerrar
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
