import React, { useState } from "react";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import { authFetch } from "auth";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import {
  FormControl,
  Grid,
  InputLabel,
  MenuItem,
  Select,
} from "@material-ui/core";

export default function FormDialog({
  espece,
  lot,
  handleSnackbar,
  setMaj,
  maj,
  mortTheo,
}) {
  const [open, setOpen] = useState(false);
  const [stats, setStats] = useState({
    date: "",
    typeStat: "",
    quantitelb: "",
    grpoisson: "",
    mortalite: "",
    commentaire: "",
  });

  const handleConfirm = () => {
    if (stats.date.length !== 10) {
      handleSnackbar("danger", "Formato de fecha incorrecto");
    } else {
      var url = "/api/stats";
      var quantitelb = 0;
      if (stats.grpoisson !== "") {
        quantitelb = 454 / parseFloat(stats.grpoisson);
      } else {
        quantitelb = parseFloat(stats.quantitelb);
      }
      console.log(stats);
      const requestOptions = {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          ...stats,
          lot: lot,
          quantitelb: quantitelb,
          date:
            stats.date.substring(6, 10) +
            "-" +
            stats.date.substring(3, 5) +
            "-" +
            stats.date.substring(0, 2),
          mortalite:
            stats.mortalite !== ""
              ? parseFloat(stats.mortalite) / 100
              : mortTheo,
        }),
      };
      authFetch(url, requestOptions)
        .then((response) => {
          if (response.ok) {
            return response.json();
          } else {
            throw new Error(response.statusText);
          }
        })
        .then((response) => {
          handleSnackbar("success", response.message);
          setMaj(~maj);
        })
        .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
      setOpen(false);
      setStats({
        date: "",
        typeStat: "",
        quantitelb: "",
        grpoisson: "",
        mortalite: "",
        commentaire: "",
      });
    }
  };

  return (
    <div>
      <Button
        id="ButtonDialog"
        variant="contained"
        color="secondary"
        onClick={() => setOpen(true)}
      >
        Añadir estadistica
      </Button>
      <Dialog
        open={open}
        onClose={() => {
          setOpen(false);
        }}
        aria-labelledby="form-dialog-title"
      >
        <DialogTitle id="form-dialog-title">Añadir estadistica</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Elija la fecha de la estadistica y la cantidad por libras.
          </DialogContentText>
          <Grid container spacing={1} justify="center" alignItems="center">
            <Grid item xs={12}>
              <TextField
                required
                label="Fecha dd/MM/yyyy"
                type="text"
                value={stats.date}
                onChange={(e) => setStats({ ...stats, date: e.target.value })}
              />
            </Grid>
            <Grid item xs={12}>
              <FormControl style={{ minWidth: "200px" }} required>
                <InputLabel id="Type-label">Typo de estadistica</InputLabel>
                <Select
                  labelId="Type-label"
                  id="Type-select"
                  value={stats.typeStat}
                  onChange={(e) =>
                    setStats({ ...stats, typeStat: e.target.value })
                  }
                >
                  <MenuItem value={"S"}>S</MenuItem>
                  <MenuItem value={"E"}>E</MenuItem>
                </Select>
              </FormControl>
            </Grid>
            <Grid item xs={12}>
              {espece &&
              (espece.substring(0, 7) === "Tilapia" ||
                espece.substring(espece.length - 7, espece.length) ===
                  "tilapia" ||
                espece.substring(0, 5) === "Carpe" ||
                espece.substring(0, 9) === "Colossoma") ? (
                <TextField
                  required
                  label="Gr par poissons"
                  type="text"
                  value={stats.grpoisson}
                  onChange={(e) =>
                    setStats({
                      ...stats,
                      grpoisson: e.target.value,
                    })
                  }
                />
              ) : (
                <TextField
                  required
                  label="Cantitad por libras"
                  type="text"
                  value={stats.quantitelb}
                  onChange={(e) =>
                    setStats({
                      ...stats,
                      quantitelb: e.target.value,
                    })
                  }
                />
              )}
            </Grid>
            <Grid item xs={12}>
              <TextField
                label="Mortalidad"
                type="text"
                value={stats.mortalite}
                onChange={(e) => {
                  setStats({
                    ...stats,
                    mortalite: e.target.value,
                  });
                }}
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                label="Comentario"
                type="text"
                value={stats.commentaire}
                onChange={(e) =>
                  setStats({ ...stats, commentaire: e.target.value })
                }
              />
            </Grid>
          </Grid>
        </DialogContent>
        <DialogActions>
          <Button
            onClick={() => {
              setOpen(false);
            }}
            color="primary"
          >
            Annuler
          </Button>
          <Button onClick={handleConfirm} color="primary">
            Ajouter
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
