import React, { useState, useEffect } from "react";
import { authFetch } from "auth";
import Button from "@material-ui/core/Button";
import {
  Grid,
  Card,
  CardHeader,
  Typography,
  TableContainer,
  TableHead,
  TableRow,
  TableCell,
  TableBody,
  Paper,
  Table,
  TextField,
} from "@material-ui/core";

import Snackbar from "components/Snackbar/Snackbar.js";
import CustomTableDelete from "./TableDelete";
import CardBody from "./Card/CardBody";
import AddSemisDialog from "./Dialog/AddSemisDialog";
import AddPechesDialog from "./Dialog/AddPechesDialog";
import AddStatsDialog from "./Dialog/AddStatsDialog";
import AddLotDialog from "./Dialog/AddLotDialog";
import CustomTableEditDelete from "./TableEditDelete";

export default function Lots({ cycle_, resetCycle }) {
  const [cycle, setCycle] = useState(cycle_.toString());
  const [lot, setLot] = useState("");
  const [comm, setComm] = useState("");
  const [lotsMenu, setLotsMenu] = useState([]);

  const [tc, setTC] = useState(false);
  const [message, setMessage] = useState("");
  const [color, setColor] = useState("primary");
  const [data, setData] = useState({ peches: [], semis: [], statistiques: [] });
  const [espece, setEspece] = useState("Tilapia");
  const [maj, setMaj] = useState(true);
  const [majLots, setMajLots] = useState(true);
  const [total, setTotal] = useState(undefined);

  useEffect(() => {
    var url = "/api/ancienLots/" + cycle;
    authFetch(url)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        setLotsMenu(response.lotsList);
        console.log(response.lotsList);
        setLot(response.lotsList[0][2]);
        setComm(response.lotsList[0][3]);
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
  }, [cycle, majLots]);

  const handleSnackbar = (cl, ms) => {
    setColor(cl);
    setMessage(ms);
    setTC(true);
    setTimeout(function () {
      setTC(false);
      setColor("");
      setMessage("");
    }, 4000);
  };

  useEffect(() => {
    if (lot !== undefined && lot !== "") {
      var url = "/api/lotData/" + lot;
      authFetch(url)
        .then((response) => {
          if (response.ok) {
            return response.json();
          } else {
            throw new Error(response.statusText);
          }
        })
        .then((response) => {
          setEspece(response.espece ? response.espece[0] : "");
          setData({
            peches: response.peches,
            semis: response.semis,
            statistiques: response.statistiques,
          });
          setTotal(response.total);
        })
        .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
    }
  }, [lot, maj]);

  const endLot = () => {
    var url = "/api/lots";
    var mortalite =
      ((total.totalSemis[1] - total.totalPeches[1]) * total.totalActuel[3]) /
      (total.totalSemis[1] - total.totalPeches[1] - total.totalActuel[0]);

    const dateToday = new Date().toISOString().substring(0, 10);
    const requestOptions = {
      method: "PUT",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        id: lot,
        quantitelb:
          total.totalActuel[1] === 0
            ? 0
            : total.totalActuel[0] / total.totalActuel[1],
        mortalite: mortalite,
        date:
          data.peches.length > 0
            ? data.peches[data.peches.length - 1].fecha
            : dateToday.substring(8) +
              "/" +
              dateToday.substring(5, 7) +
              "/" +
              dateToday.substring(0, 4),
      }),
    };
    authFetch(url, requestOptions)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        handleSnackbar("success", response.message);
        setMajLots(~majLots);
        setMaj(~maj);
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
  };

  const handleConfirm = () => {
    var url = "/api/alimentation";

    const requestOptions = {
      method: "PUT",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        id: lot,
        poids: total.alimentation,
      }),
    };
    authFetch(url, requestOptions)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        handleSnackbar("success", response.message);
        setMaj(~maj);
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
  };

  return (
    <div id="lot">
      <Snackbar
        place="tc"
        color={color}
        message={message}
        open={tc}
        closeNotification={() => setTC(false)}
        close
      />
      <Grid container spacing={1} justify="center" alignItems="center">
        <Grid item xs={2} align="center">
          <Typography variant="h4">{espece}</Typography>
        </Grid>
        <Grid item xs={2} align="center">
          <Typography variant="p">{comm}</Typography>
        </Grid>
        <Grid container spacing={1} item xs={4} align="center" justify="center">
          {lotsMenu.map((Lot, key) => (
            <Grid key={key} item>
              <Button
                key={key + 1000}
                variant="contained"
                color={Lot[5] === 1 ? "green" : "secondary"}
                onClick={() => setLot(Lot[2])}
              >
                {Lot[4]}
              </Button>
            </Grid>
          ))}
          <Grid item>
            <AddLotDialog
              cycle={cycle}
              handleSnackbar={handleSnackbar}
              setMaj={setMajLots}
              maj={majLots}
            />
          </Grid>
          <Grid item>
            <Button variant="contained" color="primary" onClick={resetCycle}>
              Back
            </Button>
          </Grid>
        </Grid>
        <Grid item xs={6} container align="center">
          {/* Semis */}
          <Card style={{ width: "100%" }}>
            <CardHeader
              style={{ padding: "8px", paddingRight: "16px" }}
              id="Header"
              color="secondary"
              title="Siembra"
              action={
                <AddSemisDialog
                  lot={lot}
                  handleSnackbar={handleSnackbar}
                  setMaj={setMaj}
                  maj={maj}
                  espece={espece}
                />
              }
            />
            <CardBody style={{ padding: 0 }}>
              <CustomTableDelete
                tableHead={[
                  "fecha",
                  "peso",
                  "cant/lb",
                  "gr/unidad",
                  "cantidad",
                  "comentario",
                ]}
                tableHeaderColor="primary"
                tableData={data.semis}
                postUrl={"/api/lotData/Semis"}
                newLine={true}
                info_for_post={lot}
                setMaj={setMaj}
                maj={maj}
              />
            </CardBody>
          </Card>
        </Grid>
        <Grid item xs={6} container align="center">
          {/* Statistiques */}
          <Card style={{ width: "100%" }}>
            <CardHeader
              style={{ padding: "8px", paddingRight: "16px" }}
              id="Header"
              color="info"
              title="Estadísticas"
              action={
                <AddStatsDialog
                  lot={lot}
                  handleSnackbar={handleSnackbar}
                  setMaj={setMaj}
                  maj={maj}
                  espece={espece}
                  mortTheo={total ? total.totalActuel[3] : null}
                />
              }
            />
            <CardBody style={{ padding: 0 }}>
              <CustomTableDelete
                tableHead={[
                  "fecha",
                  "typoestat",
                  "cant/lb",
                  "gr/unidad",
                  "mortalidad",
                  "comentario",
                ]}
                tableHeaderColor="primary"
                tableData={data.statistiques}
                postUrl={"/api/lotData/Statistiques"}
                newLine={true}
                info_for_post={lot}
                setMaj={setMaj}
                maj={maj}
              />
            </CardBody>
          </Card>
        </Grid>
        <Grid item xs={6} container align="center">
          {/* Peches */}
          <Card style={{ width: "100%" }}>
            <CardHeader
              style={{ padding: "8px", paddingRight: "16px" }}
              id="Header"
              color="primary"
              title="Pescas"
              action={
                <AddPechesDialog
                  lot={lot}
                  handleSnackbar={handleSnackbar}
                  setMaj={setMaj}
                  maj={maj}
                  espece={espece}
                />
              }
            />
            <CardBody style={{ padding: 0 }}>
              <CustomTableEditDelete
                tableHead={[
                  "fecha",
                  "peso",
                  "cant/lb",
                  "gr/unidad",
                  "cantidad",
                  "comentario",
                  "destino",
                ]}
                tableHeaderColor="primary"
                tableData={data.peches}
                postUrl={"/api/peches/" + lot}
                deleteUrl={"/api/lotData/Peches"}
                newLine={true}
                setMaj={setMaj}
                maj={maj}
              />
            </CardBody>
          </Card>
        </Grid>
        <Grid item xs={6} container align="center">
          {/* Total */}
          <Card style={{ width: "100%" }}>
            <CardHeader
              style={{ padding: "8px", paddingRight: "16px" }}
              id="Header"
              color="primary"
              title="Total"
              action={
                <Button
                  variant="contained"
                  color="secondary"
                  onClick={() => endLot()}
                >
                  Terminar el lote
                </Button>
              }
            />
            <CardBody style={{ padding: 0 }}>
              {total ? (
                <TableContainer component={Paper}>
                  <Table aria-label="total">
                    <TableHead>
                      <TableRow>
                        <TableCell>{""}</TableCell>
                        <TableCell>animales</TableCell>
                        <TableCell>libras</TableCell>
                        <TableCell>
                          {espece.substring(0, 10) === "Postlarves" ||
                          espece.substring(0, 9) === "Juvéniles"
                            ? "animales por libras"
                            : "grpoisson"}
                        </TableCell>
                      </TableRow>
                    </TableHead>
                    <TableBody>
                      <TableRow>
                        <TableCell>Siembra</TableCell>
                        <TableCell>{total.totalSemis[1]}</TableCell>
                        <TableCell>{total.totalSemis[0]}</TableCell>
                        <TableCell>
                          {espece.substring(0, 10) === "Postlarves" ||
                          espece.substring(0, 9) === "Juvéniles"
                            ? Math.round(
                                (total.totalSemis[1] / total.totalSemis[0]) * 10
                              ) / 10
                            : Math.round(
                                ((total.totalSemis[0] * 454) /
                                  total.totalSemis[1]) *
                                  10
                              ) / 10}
                        </TableCell>
                      </TableRow>
                      <TableRow>
                        <TableCell>Pesca</TableCell>
                        <TableCell>{total.totalPeches[1]}</TableCell>
                        <TableCell>{total.totalPeches[0]}</TableCell>
                        <TableCell>
                          {espece.substring(0, 10) === "Postlarves" ||
                          espece.substring(0, 9) === "Juvéniles"
                            ? Math.round(
                                (total.totalPeches[1] / total.totalPeches[0]) *
                                  10
                              ) / 10
                            : Math.round(
                                ((total.totalPeches[0] * 454) /
                                  total.totalPeches[1]) *
                                  10
                              ) / 10}
                        </TableCell>
                      </TableRow>
                      <TableRow>
                        <TableCell>Actual teo</TableCell>
                        <TableCell>
                          {total.totalActuel[0] < 0 ? 0 : total.totalActuel[0]}
                        </TableCell>
                        <TableCell>
                          {total.totalActuel[1] < 0 ? 0 : total.totalActuel[1]}
                        </TableCell>
                        <TableCell>
                          {total.totalActuel[0] <= 0 ||
                          total.totalActuel[1] <= 0
                            ? 0
                            : espece.substring(0, 10) === "Postlarves" ||
                              espece.substring(0, 9) === "Juvéniles"
                            ? Math.round(
                                (total.totalActuel[0] / total.totalActuel[1]) *
                                  10
                              ) / 10
                            : Math.round(
                                ((total.totalActuel[1] * 454) /
                                  total.totalActuel[0]) *
                                  10
                              ) / 10}
                        </TableCell>
                      </TableRow>
                      <TableRow>
                        <TableCell>Actual stat</TableCell>
                        <TableCell>
                          {data.statistiques && data.statistiques.length > 0
                            ? Math.round(
                                total.totalSemis[1] -
                                  ((total.totalSemis[1] -
                                    total.totalPeches[1] -
                                    total.totalActuel[0]) *
                                    parseFloat(
                                      data.statistiques[
                                        data.statistiques.length - 1
                                      ].mortalidad.substring(-2)
                                    )) /
                                    100 /
                                    total.totalActuel[3]
                              ) -
                                total.totalPeches[1] >
                              0
                              ? Math.round(
                                  total.totalSemis[1] -
                                    ((total.totalSemis[1] -
                                      total.totalPeches[1] -
                                      total.totalActuel[0]) *
                                      parseFloat(
                                        data.statistiques[
                                          data.statistiques.length - 1
                                        ].mortalidad.substring(-2)
                                      )) /
                                      100 /
                                      total.totalActuel[3]
                                ) - total.totalPeches[1]
                              : 0
                            : null}
                        </TableCell>
                        <TableCell>
                          {/* console.log(
                                total.totalSemis[1],
                                total.totalPeches[1],
                                total.totalActuel[0],
                                parseFloat(
                                  data.statistiques[
                                    data.statistiques.length - 1
                                  ].mortalidad.substring(-2)
                                ) / 100,
                                total.totalActuel[3],
                                total.totalPeches[1],
                                data.statistiques[data.statistiques.length - 1][
                                  "cant/lb"
                                ]
                              ) */}
                          {data.statistiques && data.statistiques.length > 0
                            ? Math.round(
                                (total.totalSemis[1] -
                                  ((total.totalSemis[1] -
                                    total.totalPeches[1] -
                                    total.totalActuel[0]) *
                                    parseFloat(
                                      data.statistiques[
                                        data.statistiques.length - 1
                                      ].mortalidad.substring(-2)
                                    )) /
                                    100 /
                                    total.totalActuel[3] -
                                  total.totalPeches[1]) /
                                  data.statistiques[
                                    data.statistiques.length - 1
                                  ]["cant/lb"]
                              ) > 0
                              ? Math.round(
                                  (total.totalSemis[1] -
                                    ((total.totalSemis[1] -
                                      total.totalPeches[1] -
                                      total.totalActuel[0]) *
                                      parseFloat(
                                        data.statistiques[
                                          data.statistiques.length - 1
                                        ].mortalidad.substring(-2)
                                      )) /
                                      100 /
                                      total.totalActuel[3] -
                                    total.totalPeches[1]) /
                                    data.statistiques[
                                      data.statistiques.length - 1
                                    ]["cant/lb"]
                                )
                              : 0
                            : null}
                        </TableCell>
                        <TableCell>
                          {data.statistiques && data.statistiques.length > 0
                            ? espece.substring(0, 10) === "Postlarves" ||
                              espece.substring(0, 9) === "Juvéniles"
                              ? data.statistiques[data.statistiques.length - 1][
                                  "cant/lb"
                                ]
                              : Math.round(
                                  data.statistiques[
                                    data.statistiques.length - 1
                                  ]["gr/unidad"] * 10
                                ) / 10
                            : null}
                        </TableCell>
                      </TableRow>
                      <TableRow>
                        <TableCell colSpan={2} align="right">
                          Mort teo / stat
                        </TableCell>
                        <TableCell>
                          {Math.round(total.totalActuel[3] * 1000) / 10} %
                        </TableCell>
                        <TableCell>
                          {data.statistiques && data.statistiques.length > 0
                            ? data.statistiques[data.statistiques.length - 1]
                                .mortalidad
                            : null}
                        </TableCell>
                      </TableRow>
                      <TableRow>
                        <TableCell colSpan={2} align="right">
                          Alimentacion teo / stat
                        </TableCell>
                        <TableCell>
                          {total.totalActuel[4]} lbs{" "}
                          {Math.round(total.totalActuel[5] * 10000) / 100} %
                        </TableCell>
                        <TableCell>
                          {Math.round(total.alimentationstat * 10) / 10} lbs{" "}
                          {Math.round(total.alimentationstatpourc * 10000) /
                            100}{" "}
                          %
                        </TableCell>
                        <TableCell>
                          <TextField
                            style={{ maxWidth: "50px" }}
                            label="Dar  "
                            type="text"
                            value={total.alimentation}
                            onChange={(e) =>
                              setTotal({
                                ...total,
                                alimentation: e.target.value,
                              })
                            }
                          />
                        </TableCell>
                      </TableRow>
                      <TableRow>
                        <TableCell colSpan={2} />
                        <TableCell>Tiempo</TableCell>
                        <TableCell>{total.totalActuel[2]} meses</TableCell>
                        <TableCell>
                          <Button onClick={handleConfirm} color="primary">
                            V
                          </Button>
                        </TableCell>
                      </TableRow>
                    </TableBody>
                  </Table>
                </TableContainer>
              ) : null}
            </CardBody>
          </Card>
        </Grid>
      </Grid>
    </div>
  );
}
