import React, { useState, useEffect } from "react";
import "assets/css/App.css";
import { authFetch } from "auth";
import CustomTableEdit from "components/TableEdit";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import AddEnregistrementDB from "components/Dialog/AddEnregistrementDB";
import CardBody from "components/Card/CardBody.js";
import {
  Button,
  Grid,
  Typography,
  FormControl,
  Select,
  InputLabel,
  MenuItem,
  FormHelperText,
} from "@material-ui/core";
import Snackbar from "components/Snackbar/Snackbar.js";

export default function Table() {
  const [table, setTable] = useState("Bassins");
  const [tableHeaders, setTableHeaders] = useState("");
  const [tableData, setTableData] = useState("");

  const [maj, setMaj] = useState(true);
  const [message, setMessage] = useState("");
  const [color, setColor] = useState("primary");
  const [tc, setTC] = useState(false);

  useEffect(() => {
    var url = "/api/table/" + table;
    authFetch(url)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        setTableHeaders(response.headers);
        setTableData(response.data);
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
    setMaj(false);
  }, [maj, table]);

  const handleTableChange = (e) => {
    setTable(e.target.value);
  };

  const handleSnackbar = (cl, ms) => {
    setColor(cl);
    setMessage(ms);
    setTC(true);
    setTimeout(function () {
      setTC(false);
      setColor("");
      setMessage("");
    }, 4000);
  };

  return (
    <div id="#copeyito">
      <Snackbar
        place="tc"
        color={color}
        message={message}
        open={tc}
        closeNotification={() => setTC(false)}
        close
      />
      <Grid container spacing={1}>
        <Grid item xs={12} align="center" container>
          <Grid item xs={2} align="center">
            <FormControl>
              <InputLabel id="table-label">Table</InputLabel>
              <Select
                labelId="table-label"
                id="table-select"
                value={table}
                onChange={handleTableChange}
              >
                <MenuItem value={"Bassins"}>Bassins</MenuItem>
                <MenuItem value={"Especes"}>Espèces</MenuItem>
                <MenuItem value={"Pompes"}>Pompes</MenuItem>
                <MenuItem value={"Mortalite"}>Mortalité</MenuItem>
                <MenuItem value={"TypeAliment"}>Type d'aliments</MenuItem>
                <MenuItem value={"Croissance"}>Croissance</MenuItem>
              </Select>
              <FormHelperText>Sélectionner la table à afficher</FormHelperText>
            </FormControl>
          </Grid>
          <Grid item xs={6} align="left">
            <AddEnregistrementDB
              type={table}
              handleSnackbar={handleSnackbar}
              setMaj={setMaj}
              maj={maj}
            />
          </Grid>
        </Grid>
        <Grid item xs={12} align="center">
          {tableHeaders === "" || tableData === "" ? null : (
            <Card>
              <CardHeader color="info">
                <Typography variant="h4">{table}</Typography>
              </CardHeader>
              <CardBody>
                <CustomTableEdit
                  tableHeaderColor="info"
                  tableHead={tableHeaders}
                  tableData={tableData}
                  postUrl={"/api/table/" + table}
                />
              </CardBody>
            </Card>
          )}
        </Grid>
      </Grid>
    </div>
  );
}
